<template>
  <div id="div_follwup_case">
    <!--分类-->
    <div class="time_list border-1px">
      <ul>
        <li v-for="(item,index) in this.timeList" :class="{selected: index === currentIndex}" class="time_list_item"  @click="itemClick(index)">{{item}}</li>
      </ul>
    </div>
    <!--患者详情-->
    <div class="patientInfo border-1px">
      <div class="patient-date">
        入院日期：<span class="patient_conent">2017-11-20 11:30</span>
      </div>
      <div class="patient-item">
        <p>
          <span class="patient_title">病史陈述者：</span>
          <span class="patient_conent">本人</span>
        </p>
        <p>
          <span class="patient_title">主诉：</span>
          <span class="patient_conent">对声反应差伴学语异常4年</span>
        </p>
        <p style="margin-bottom: 0">
          <span class="patient_title">现病史：</span>
          <span class="patient_conent">
            4年前患者出生未行听力筛选，患儿对声一直反应差，仅能发"啊""咦"等单音字节，遂至当医院就诊，双耳佩戴助听器并入康复学校学习后效果欠佳，语言分辨能力差，学语未见提高，先一步
            诊治来我院就诊
            </span>
        </p>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
      name: 'FollwupCase',
      methods: {
        itemClick: function (index) {
          this.currentIndex = index;
        }
      },
      data () {
          return {
              currentIndex: 0,
              timeList: ['16/4/9', '16/4/8', '16/4/7', '17/4/6'] // 用来存储看病日期数据的数组
          };
      }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  @import "../common/stylus/mixin.styl";
  #div_follwup_case
    background-color: #f5f5f5
    .time_list
      position:relative
      background-color: #ffffff
      margin: 15px 0
      display: flex
      flex-direction: row
      padding-left:15px
      color: #999999
      border-top-1px(#e5e5e5)
      border-bottom-1px(#e5e5e5)
      .time_list_item
         list-style: none
         display: inline-block
         text-align: center
         line-height: 50px
         margin-right: 35px
         color: #999999
      .selected
        color:#333
    .patientInfo
      position:relative
      height: auto
      border-top-1px(#e5e5e5)
      .patient-date
        line-height: 50px
        background-color: #ffffff
        font-size: 15px
        padding-left: 15px
        color: #666666
    .patient-item
      height: auto
      background-color: #ffffff
      margin-top: 1px
      padding: 20px 15px
      .patient_title
         color:#666666
      .patient_conent
         color:#333333
    .patient-item p
      color: #999999
      margin-bottom: 20px
      line-height: 30px
</style>



