<template>
  <div id="BusinessInfo">
    <lg-preview></lg-preview>
    <div class="calendar" ref="tabbarWrapper" :style="{width:this.ScreenWidth+'px'}">
      <ul class="date-list" :style="{width:(this.tabbarConentWidth + 'px')}">
        <li v-for="(item,index) in this.dates" :class="{isCheck:index === current}" @click="itemClick(index)">
          <div>
            <span>{{item.weekday}}</span>
            <span>{{item.dateStr}}</span>
          </div>
        </li>
      </ul>
    </div>
    <div class="detail">
      <div class="title" >
        <div class="left-title">
          <span class="doctor">张晓明医生</span>
        </div>
        <span class="time">5:30AM</span>
      </div>
      <div class="content" v-show="status">
        <div class="voice">
          <img  v-if="isShow" @click="playOrPaused()" src="../assets/play@2x.png"  width="22px" height="22px" id="js-play">
          <img @click="playOrPaused()" src="../assets/Stop_it@2x.png"  width="22px" height="22px"  v-else>

          <div class="play-box">
            <div class="timeline" id="musicAudioProgressPar">
              <div id="musicAudioProgress" style="height: 1px; background-color: #5baaec" >
                <span></span>
              </div>
            </div>
            <div class="info">
              <span class="timeshow" id="currentTime">00:24:56</span>
            </div>
          </div>
          <audio style="display: none" src="http://chipsguide.snaillove.com/Public/Uploads/file_server_storage/Audio/2017/03/23/19/192_20003137.mp3" id="audio" ></audio>
        </div>
        <div class="Imglist"   :style="{width:this.ScreenWidth+'px'}">
          <span v-for="(item,index) in this.dataSource.imgList" :style="{marginLeft:MarginLeft+'px'}">
            <img  v-preview="item" :src="item">
          </span>
        </div>
      </div>
      <div class="slide">
        <img v-if="show" src="../assets/gengduo_xai@2x.png" width="16px" height="8px" @click="slideUp()" >
        <img  src="../assets/gengduo_shang@2x.png" width="16px" height="8px" @click="slideUp()" v-else>
      </div>
    </div>
  </div>
</template>

<script>
  import BScroll from 'better-scroll';
  import {createDates} from '../common/js/date.js';
  import MyTabbar from './Tabbar/MyTabbar.vue';
  export default{
    name: 'BusinessInfo',
    components: {
      't-tabbar': MyTabbar
    },
    data() {
      return {
        isShow: false,
        rangeValue: 0,
        show: false,
        status: true,
        current: 0,
        ScreenWidth: document.body.clientWidth,
        tabbarConentWidth: 980,
        MarginLeft: 4,
        progressTimer: 0,
        audio: '',
        list: [
          {
            recordId: 123,
            doctorId: 111,
            name: '张晓明医生',
            time: '10月20日 5:30AM'
          },
          {
            recordId: 123,
            doctorId: 111,
            name: '李明达医生',
            time: '10月22日 5:30AM'
          }
        ],
        dates: [
          {
            weekday: '',
            dateStr: '',
            date: new Date()
          }
        ],
        dataSource: {
          voice: [],
          imgList: [
            'http://image.photophoto.cn/nm-7/013/019/0130190021.jpg',
            'http://image.photophoto.cn/nm-7/013/019/0130190021.jpg',
            'http://image.photophoto.cn/nm-7/013/019/0130190021.jpg',
            'http://image.photophoto.cn/nm-7/013/019/0130190021.jpg',
            'http://image.photophoto.cn/nm-7/013/019/0130190021.jpg',
            'http://image.photophoto.cn/nm-7/013/019/0130190021.jpg',
            'http://image.photophoto.cn/nm-7/013/019/0130190021.jpg',
            'http://image.photophoto.cn/nm-7/013/019/0130190021.jpg'
          ]
        }
      };
    },
    methods: {
      itemClick: function (index) {
        if (this.current === index) return;
        this.current = index;
      },
      initImgWidth: function () {
        // 获取屏幕宽度
//        this.ScreenWidth = window.screen.availWidth;
//        this.ScreenWidth = document.documentElement.clientWidth;
//        console.log(this.ScreenWidth);
        let conentWidth = 0;
        let childrenList = document.getElementsByClassName('date-list')[0].children;
        for (let i = 0; i < childrenList.length; i++) {
          conentWidth += childrenList[i].clientWidth + 2;
        }
        console.log(conentWidth);
        this.tabbarConentWidth = conentWidth;
        // 获取marginleft值
        let cols = 4;
        let LiWidth = 75;
        this.MarginLeft = (this.ScreenWidth - cols * LiWidth) / (cols + 1);
        console.log(this.MarginLeft);
      },
      initBScroll: function () {
        if (!this.$refs.tabbarWrapper) {
          return;
        }
        /* 利用better-scroll处理滑动 */
        this.mScroll = new BScroll(this.$refs.tabbarWrapper, { bounce: false, scrollX: true, scrollY: false, click: true });
      },
      slideUp: function () {
        this.status = !this.status;
        this.show = !this.show;
      },
      // 暂停或播放
      playOrPaused: function () {
        this.isShow = !this.isShow;
        if (this.audio.paused) {
          this.audio.play();
          this.progressTimer = window.setInterval(this.audioProgress, 100);
          return;
        }
        this.audio.pause();
      },
      audioProgress: function () {
        if (this.audio.currentTime < this.audio.duration) {
          if (this.audio.played) {
            // 更新当前时间
            this.getCurrentTime();
            // 更新进度条
            this.updateProgressVal();
          }
        } else {
          clearInterval(this.progressTimer);
        }
      },
      // 初始化页面
      initAudio: function () {
        // 初始化对象
        let audio = document.getElementById('audio');
        this.audio = audio;
        // 初始化当前时间
        this.getCurrentTime();
      },
      // 更新进度条当前值
      updateProgressVal: function () {
         let offsetWidth = document.getElementById('musicAudioProgressPar').offsetWidth;
        let progressVal = this.audio.currentTime / this.audio.duration * offsetWidth;
        document.getElementById('musicAudioProgress').style.width = progressVal + 'px';
      },
      // 更新当前时间
      getCurrentTime: function () {
        let currentTime = this.audio.currentTime;
        let totalTime = parseInt(currentTime / 60);
        let seconds = parseInt(currentTime % 60);
        if (totalTime === 0) {
          if (seconds > 9) {
            totalTime = '00:' + seconds;
          } else {
            totalTime = '00:0' + seconds;
          }
        } else {
          let totalTimePre, totalTimeNext;
          if (totalTime > 9) {
            totalTimePre = totalTime;
          } else {
            totalTimePre = '0' + totalTime;
          }
          if (seconds > 9) {
            totalTimeNext = seconds;
          } else {
            totalTimeNext = '0' + seconds;
          }
          totalTime = totalTimePre + ':' + totalTimeNext;
        }
        document.getElementById('currentTime').innerText = totalTime;
      }
    },
    mounted () {
      const that = this;
      window.onresize = () => {
        return (() => {
          window.screenWidth = document.body.clientWidth;
          that.ScreenWidth = window.screenWidth;
          console.log(that.ScreenWidth);
          // 获取marginleft值
          let cols = 4;
          let LiWidth = 75;
          that.MarginLeft = (that.ScreenWidth - cols * LiWidth) / (cols + 1);
          console.log(that.MarginLeft);
        })();
      };
      this.initImgWidth();
      this.$nextTick(() => {
          this.initBScroll();
      });
      this.initAudio();
    },
    watch: {
        ScreenWidth (val) {
          if (!this.timer) {
            this.ScreenWidth = val;
            this.timer = true;
            let that = this;
            setTimeout(function () {
              that.timer = false;
            }, 400);
          }
        }
    },
    created() {
      createDates(this.dates, 14);
      for (let i = 0; i < this.dates.length; i++) {
        this.dates[i].meridiem = i % 3;
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../common/stylus/index.styl"
  #BusinessInfo
    background-color: #f5f5f5
    overflow-x: auto
  .calendar
    box-sizing: border-box
    white-space: nowrap
    overflow: hidden
    margin-bottom: 15px
    padding-top: 15px
  .calendar ul li
    display: inline-block
    width: 50px
    height: 50px
    box-sizing: border-box
    border: 1px solid #e5e5e5
    background-color: #ffffff
    text-align: center
    color: #333333
    &:first-child
      color: #5baaec
    &.isCheck
      background-color: #5baaec
      border: none
      color: #ffffff
  .calendar ul li div
    width: 100%
    height: 100%
    display: flex
    font-size: 15px
    flex-direction: column
    justify-content: center
    align-items: center
  .calendar ul li div span:last-child
    font-size: 12px
    margin-top: 5px
  .detail
    height: auto
    margin-bottom: 15px;
  .detail .title
    display: flex
    flex-direction: row
    align-items: center
    padding: 0 15px
    box-sizing: border-box
    height: 30px
    margin-bottom: 1px
    background-color: #ffffff
  .left-title
    flex: 0.6
    line-height: 30px
    flex-direction: row
    align-items: center
    &>span
      color: #333333
      font-size: 15px
  .time
    flex: 0.4
    text-align: right
    font-size: 13px
    color: #999
  .content
    padding: 10px 0 0 0
    background-color: #ffffff
    margin-top: 1px
    box-sizing: border-box
    margin-bottom: 0

  .voice
    margin: 10px 0
    display: flex
    flex-direction: row
    padding: 0 15px
    align-items: center
    box-sizing: border-box
  .play-box
    display: flex
    flex: 1
    margin-left: 15px
    flex-direction: row
    align-items: center
  .play-box .timeline
    flex: 0.9
    height: 1px
    background-color: #e5e5e5;
    position: relative
    /*z-index: 2;*/
  .play-box .timeline>#musicAudioProgress
    position: absolute
    left: 0
    top: 0
  .play-box .timeline>#musicAudioProgress span:after
    content: ''
    position: absolute
    top: -2px
    right: 0
    height: 4px
    width: 4px
    border-radius: 50%
    background-color: #5baaec
  .play-box .info
    flex: 0.1
    text-align: right
    margin-left: 5px
    &>.timeshow
      color: #666666
      font-size: 12px

  .Imglist
    box-sizing: border-box
    display: flex
    flex-direction: row
    flex-wrap: wrap
    overflow: hidden;
  .Imglist span
    display: inline-block
    height: 55px
    margin-bottom: 25px
    width: 75px
    box-sizing: border-box
    border-radius: 5px
    border: 1px solid #e5e5e5
    &>img
      width: 100%;
      height: 100%
      object-fit: content
      border-radius: 5px
  .Imglist span:nth-last-child(1),
  .Imglist span:nth-last-child(2),
  .Imglist span:nth-last-child(3),
  .Imglist span:nth-last-child(4)
    margin-bottom: 0
  .slide
    display: flex
    justify-content: center
    align-items: center
    background-color: #ffffff
  .slide img
    margin: 15px 0
</style>



