<template>
  <div id="div_reviewinfo" v-windowTitle  :data-title="this.title">
    <t-tabbar :tabItems="this.tabItems" @introduce="tabbarItemClick"></t-tabbar>
    <div  v-if="showLoadding"  class="loading">
      <mt-spinner type="triple-bounce" color="#3293BE" ></mt-spinner>
    </div>
    <scroller  v-if="pListData.length>0"  :on-refresh="refresh" :on-infinite="infinite" ref="myscroller" style="margin-top:46px">
    <div class="p_list">
      <div class="p_list_item" v-for="(item,index) in this.pListData">
          <p class="list_item_title border-1px" @click="toDetail(item)" >
             <span class="p_name">{{item.project}}</span>
             <img class="more_icon" src="~assets/item_more_icon.png"   width="10px" height="20px"/>
          </p>
           <div class="list_item_detail">
            <p class="list_item_detail_item">
              <span class="detail_item_title">研究目的：</span> <span class="detail_item_content"> {{item.info.purpose}} </span>
            </p>
             <p  class="list_item_detail_item" style="padding:15px 0px">
               <span class="detail_item_title">项目类型：</span> <span class="detail_item_content"> {{item.info.type}}</span>
             </p>
             <p class="list_item_detail_item">
               <span class="detail_item_title">项目负责人：</span><span class="detail_item_content"> {{item.info.applicant}}</span>
             </p>
           </div>
           <div class="list_item_button border-1px" v-show="item.status == 1">
             <span class="item_btn" style="margin-right:80px" @click="reviewCommand({projectId:item.id, status:2})">通过</span>
             <span class="item_btn" @click="reviewCommand({projectId:item.id, status:3})">不通过</span>
           </div>
      </div>
    </div>
    </scroller>
    <div class="empty_data color-6"  v-if="pListData.length==0">
      暂无数据
    </div>
  </div>
</template>
<script>
  import { Spinner, MessageBox } from 'mint-ui';
  import MyTabbar from './Tabbar/MyTabbar.vue';
  import { netWorkPost } from '../common/js/netjob';
  export default {
    name: 'ReviewInfo',
    components: {
      't-tabbar': MyTabbar,
      'mt-spinner': Spinner
    },
    methods: {
      tabbarItemClick: function (item) {
        console.log(item);
        this.status = item.status; // 改变状态值
      },
       /* tabItemClick: _.debounce(function (index) {
        if (this.tabItems[index].isCheck) return;
        let len = this.tabItems.length;
        for (let i = 0; i < len; i++) {
          this.tabItems[i].isCheck = false;
        }
        this.tabItems[index].isCheck = true;
        this.status = this.tabItems[index].status; // 改变状态值
      }, 200), */
      toDetail: function (item) {
        this.$router.push({ name: 'FollowupDetail', params: { doctorId: 1, projectID: item.id, projectName: item.project } });
      },
      refresh: function (done) {  //  刷新
        let vm = this;
        vm.pageNumber = 1;
        this.initProjectList({
          data: {
            'pageNumber': vm.pageNumber
          },
          complete: function () {
             done(true);
          }
        });
      },
      infinite: function (done) {  // 加载更多
        let vm = this;
        if (vm.toatalPage > vm.pageNumber) {
          vm.pageNumber++;
          this.initProjectList({
            data: {
              'pageNumber': vm.pageNumber
            },
            complete: function () {
                done();
            }
          });
        } else {
          done(true);
        }
        console.log('toatalPage' + vm.toatalPage);
        console.log('pageNumber' + vm.pageNumber);
      },
      initProjectList: function (val) {
        let vm = this;
        let doctorID = vm.$route.query.doctorid;
        let requestData = {
            'doctorId': doctorID,
            'pageNumber': vm.pageNumber,
            'pageSize': 10,
            'status': vm.status
        };
        if (val) {
          Object.assign(val.data, requestData);
        }
        let pam = {
          data: {},
          complete: function () {
          },
          success: function (res) {
            let response = res.data;
            if (response.status > 0) return;
            if (this.data.pageNumber === 1) {  // 判断是加载第一页
               vm.toatalPage = response.data.total;
               vm.pListData = response.data.list;
            } else {  // 判断是否加载更多
              let moreData = response.data.list;
              moreData.forEach(function (item) {
                vm.pListData.push(item);
              });
              console.log(vm.pListData);
            //  console.log(typeof vm.pListData);
            }
            this.complete();
          },
          fail: function (res) {
          }
        };
        pam.data = requestData;
        Object.assign(pam, val);
        this.requestProjectList(pam);
      },
      requestProjectList: function (val) {
          let vm = this;
          let pam = {
              url: 'followup/needMeReview',
              data: {},
              type: '',
              before: function () {
                vm.showLoadding = true;
              },
              success: function (res) {
              },
              fail: function (res) {
              },
              after: function () {
                vm.showLoadding = false;
              }
          };
          Object.assign(pam, val);
          pam.before();
        netWorkPost(pam);
      },
      reviewCommand: function (obj) {
        let vm = this;
        MessageBox.confirm('您确定要进行此操作吗?', '提示').then(success => {
          vm.requestReview({
            data: {
              doctorId: 1,
              projectId: obj.projectId,
              status: obj.status
            }
          });
        }, canel => {

        });
      },
      requestReview: function (val) {
        let vm = this;
        let pam = {
            url: 'followup/review',
            data: {},
            before: function () {},
            success: function (res) {
              let response = res.data;
              if (response.status > 0) {
                 this.fail(res);
                  return;
              }
              // 操作成功 重新修改列表
              vm.pageNumber = 1;
              vm.initProjectList({ data: {
                'pageNumber': vm.pageNumber
              }});
            },
            fail: function (res) {
              MessageBox.alert('操作失败!', '提示');
            },
            after: function () {
            }
        };
        Object.assign(pam, val);
        netWorkPost(pam);
      }
    },
    mounted: function() {
       this.pageNumber = 1;
       this.initProjectList({ data: {
          'pageNumber': this.pageNumber
       }});
    },
    watch: {
      status: function (newVule) {
        this.pageNumber = 1;
        this.initProjectList({ data: {
          'pageNumber': this.pageNumber
        }});
      }
    },
    activated: function () {
      document.title = '需我审核';
    },
    data () {
      return {
        tabItems: [
          { title: '待审核', status: 1, isCheck: true },
          { title: '通过', status: 2, isCheck: false },
          { title: '不通过', status: 3, isCheck: false }
        ],
        pListData: [],
        title: '需我审核',
        showLoadding: false,
        toatalPage: 1, // 总分页大小
        status: 1,  // 审核状态
        pageNumber: 1 // 每加载一次增加1，刷新时默认为1
      };
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  @import "../common/stylus/mixin.styl";
  #div_reviewinfo
    background-color:#e5e5e5
    height:100%
    .loading
      display:flex
      z-index:10
      position:absolute;
      height:100%
      width:100%
      top:0px
      left:0px
      align-items:center
      justify-content:center
    .p_list
      .p_list_item
        margin:15px 0px 0px 0px
        background-color:white
        .list_item_title
          height:48px
          line-height:48px
          padding:0px 15px
          border-top-1px(#e5e5e5)
          border-bottom-1px(#e5e5e5)
          .p_name
            color:#333333
            font-size:15px
          .more_icon
            float:right
            margin-top:10px
        .list_item_detail
          padding:17px 15px 15px 15px
          color:#666666
          font-size:15px
          .list_item_detail_item
            display:flex
            .detail_item_title
              width:100px
            .detail_item_content
              flex:1
        .list_item_button
          padding:25px 0px 20px 0px
          margin:0px 15px
          text-align:center
          border-top-1px(#e5e5e5)
          .item_btn
            display:inline-block
            width:80px
            height:20px
            line-height:20px
            text-align:center
            padding:5px
            border:1px solid #666
            border-radius:6px
            color:#666
            font-size:13px
    .empty_data
      text-align:center;
      width:100%
      padding-top:20px
</style>



