<template>
  <div id="div_patient_detail">
    <div class="top_bar" >
      <p  v-if="popupVisible">
        <span style="color: #666666">全部</span>
        <img src="../assets/gengduo_shang@2x.png" width="16px" height="10px" @click="drop" >
      </p>
      <div class="drop" @click="drop" v-else>
        <img src="../assets/drop-down@2x.png"  width="16px" height="10px" >
      </div>
    </div>

    <mt-popup class="popup" v-model="popupVisible" position="top" >
      <div class="sliderdown" >
        <ul>
          <li v-for="(item,index) in this.tabItems" :class="{actived: index===current}" @click="tabItemClick(index, true)">{{item.title}}</li>
        </ul>
      </div>
    </mt-popup>
    <div id="tab_panel" ref="tabbarWrapper"   :style="{width:this.tabbarWidth+'px'}">
      <p class="top_tab" :style="{width:(this.tabbarConentWidth + 'px')}" >
        <span class="tab_item" v-for="(item,index) in this.tabItems" :class="{isCheck:index===current}"    @click="tabItemClick(index, false)"
              :style="{borderBottomColor:(index===current? '#5baaec': '#e5e5e5'), borderBottomWidth:(index===current? '2px':'0px'), width:(item.title.length * 15) + 'px' }">
         {{item.title}}
        </span>
      </p>
      <p class="show_tab">
      </p>
      <p class="line border-1px"></p>
    </div>
    <div style="padding-top: 50px;">
   <component :is="componentData" ></component>
    </div>
  </div>
</template>
<script>
  import BScroll from 'better-scroll';
  import HospitalRecord from './HospitalRecord';
  import CourseInfo from './CourseInfo';
  import CheckResult from './CheckResult';
  import OperationInfo from './OperationInfo';
  import ImageResult from './ImageResult';
  import OtherCheckResult from './OtherCheckResult';
  import MedicalRecordHome from './MedicalRecordHome';
  import DoctorAdvice from './DoctorAdvice';
  import ThreeTestSheet from './ThreeTestSheet';
  export default {
      name: 'PatientDetail',
      components: {
        'HospitalRecord': HospitalRecord,
        'CourseInfo': CourseInfo,
        'CheckResult': CheckResult,
        'OperationInfo': OperationInfo,
        'ImageResult': ImageResult,
        'OtherCheckResult': OtherCheckResult,
        'MedicalRecordHome': MedicalRecordHome,
        'DoctorAdvice': DoctorAdvice,
        'ThreeTestSheet': ThreeTestSheet
      },
      methods: {
          drop: function () {
            this.popupVisible = !this.popupVisible;
          },
          tabItemClick: function (index, isScrollTo) {
            if (this.current === index) return;
            this.current = index;
            this.componentData = this.tabItems[index].componentsName;
            if (this.popupVisible) {
              this.popupVisible = false;
            }
            if (!isScrollTo || !this.mScroll) { return; }
            this.mScroll.scrollToElement(document.getElementsByClassName('tab_item')[index], 60, true);
//            if (this.tabItems[index].isCheck) return;
//            let len = this.tabItems.length;
//            for (let i = 0; i < len; i++) {
//              this.tabItems[i].isCheck = false;
//            }
//            this.tabItems[index].isCheck = true;
//              this.componentData = this.tabItems[index].componentsName;
          },
          initBScroll: function () {
            if (!this.$refs.tabbarWrapper) {
              return;
            }
            /* 利用better-scroll处理滑动 */
            this.mScroll = new BScroll(this.$refs.tabbarWrapper, { bounce: false, scrollX: true, scrollY: false, click: true });
          },
          initTabtWidth: function () {
            // 获取屏幕宽度
            this.tabbarWidth = window.screen.availWidth - 50;
            let conentWidth = 15; //
            let childrenList = document.getElementsByClassName('top_tab')[0].children;
            for (let i = 0; i < childrenList.length; i++) {
              // let ro = childrenList[i].getBoundingClientRect();
             // alert('Top' +ro.top + 'Bottom' + ro.bottom + 'Left' + ro.left + 'Right' + ro.right );
            //  let mWidth = ro.width || ro.Right - ro.Left;
            //  alert(mWidth);
              conentWidth += 35 + childrenList[i].offsetWidth; // 加上子项偏移量
            }
            console.log(conentWidth);
            // 获取内容宽度
            this.tabbarConentWidth = conentWidth;  //
          }
      },
      mounted () {
        this.initTabtWidth();
         this.$nextTick(() => {
          this.initBScroll();
        });
      },
      data () {
          return {
            tabbarWidth: 0,
            tabbarConentWidth: 980,
            componentData: 'HospitalRecord',
            popupVisible: false,
            current: 0,
            mScroll: null,
            tabItems: [
              { title: '入院记录', status: 1, isCheck: true, componentsName: 'HospitalRecord' },
              { title: '病程记录', status: 2, isCheck: false, componentsName: 'CourseInfo' },
              { title: '手术记录', status: 3, isCheck: false, componentsName: 'OperationInfo' },
              { title: '实验室检查结果', status: 4, isCheck: false, componentsName: 'CheckResult' },
              { title: '影像结果', status: 5, isCheck: false, componentsName: 'ImageResult' },
              { title: '其他检查结果', status: 6, isCheck: false, componentsName: 'OtherCheckResult' },
              { title: '医嘱', status: 7, isCheck: false, componentsName: 'DoctorAdvice' },
              { title: '护理三测单', status: 8, isCheck: false, componentsName: 'ThreeTestSheet' },
              { title: '病案首页', status: 9, isCheck: false, componentsName: 'MedicalRecordHome' }
            ]
          };
      }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../common/stylus/mixin.styl";
#div_patient_detail
  background-color: #f5f5f5
  position: relative
  .top_bar
    height: 50px
    background-color: #ffffff
    position: fixed
    top: 0
    right: 0
    z-index: 99999
    &>p
      height: 51px
      width: 100%
      background-color: #ffffff
      display: flex
      flex-direction: row
      padding: 0 15px
      box-sizing: border-box
      align-items: center
      justify-content: space-between
      position: fixed
      left: 0
      top: 0
    .drop
      width: 50px
      height: 50px
      background: #ffffff
      text-align: center
      line-height: 50px
      box-shadow: -2px 0px 4px #ccc;
  .popup
    width: 100%
    margin-top: 51px
  .sliderdown
    height: auto
    padding: 10px 0
    border-top: 1px solid #f5f5f5
  .sliderdown ul
    display: flex
    flex-direction: row
    flex-wrap: wrap
    justify-content: space-around
  .sliderdown ul li
    list-style: none
    width: 105px
    height: 35px
    background-color: #e5e5e5
    border-radius: 5px
    text-align: center
    line-height: 35px
    font-size: 14px
    margin-top: 10px
    margin-bottom: 10px
  .actived
    color: #ffffff
    background-color: #959493
  #tab_panel
    background-color:white
    position:fixed
    top:0px
    left:0px
    right:50px
    z-index:9998
    white-space: nowrap;//强制不折行
    overflow:hidden
    .isCheck
      color:#5baaec
    .top_tab
      color:#666
      font-size:0px
      padding-left:15px
      .tab_item
        display:inline-block
        height:48px
        line-height:48px
        text-align:center
        font-size:15px
        font-weight:600
        border-bottom-style:solid
        margin-right:35px
    .top_tab_line
      font-size:0px
      height:auto
      .tab_item_line
        width:120px
        display:inline-block
        border-bottom-style:solid
  .line
    width:100%
    height:1px
    background-color:#e5e5e5
</style>



