<template>
  <div id="VisitsInfo">
    <div id="tabPanel">
      <div class="item">
        <div class="menutitle" v-for="(item,index) in this.data">
          <p class="menuItem"  >
            <img   v-if="item.isCheck" class="da_img" src="../assets/dayuan@2x.png" style="width: 12px;height: 12px" >
            <img   class="xiao_img" src="../assets/xiaoyuan@2x.png" style="width: 8px;height: 8px" v-else>
            <span class="date-line" >{{item.date}}</span>
            <img src="../assets/gengduo_shang@2x.png" v-if="item.isCheck" style="width: 18px;height: 10px" @click="toggle(index)">
            <img src="../assets/gengduo_xai@2x.png" style="width: 18px;height: 10px" @click="toggle(index)" v-else>
          </p>
          <p class="table">{{item.record.title}}</p>
          <div class="sildermun" v-show="item.isCheck" >
            <p >{{item.record.score}}</p>
            <ul>
              <li v-for="(v,index) in item.record.list">
                <p class="question">{{v.question}}</p>
                <p class="answer">{{v.answer}}</p>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import _ from 'lodash';
  export default {
    name: 'VisitsInfo',
    components: {
    },
    data () {
      return {
        data: [
          {
            status: 1,
            isCheck: true,
            date: '2017-10-20',
            record: {
              title: '内科问诊表',
              score: 80,
              list: [
                {
                  question: '1.您现在最重要的不适是什么？持续了多长时间',
                  answer: '过敏'
                },
                {
                  question: '2.因为什么原因发病？有什么方法能缓解？',
                  answer: '吃海鲜'
                },
                {
                  question: '3.您因为目前的情况去医院做了哪些检查',
                  answer: 'B超'
                }
              ]
            }
          },
          {
            status: 1,
            isCheck: false,
            date: '2017-08-16',
            record: {
              title: '骨科问诊表',
              score: 70,
              list: [
                {
                  question: '1.您现在最重要的不适是什么？持续了多长时间',
                  answer: '过敏'
                },
                {
                  question: '2.因为什么原因发病？有什么方法能缓解？',
                  answer: '吃海鲜'
                },
                {
                  question: '3.您因为目前的情况去医院做了哪些检查',
                  answer: 'B超'
                }
              ]
            }
          },
          {
            status: 1,
            isCheck: false,
            date: '2017-07-28',
            record: {
              title: '心胸外科问诊表',
              score: 60,
              list: [
                {
                  question: '1.您现在最重要的不适是什么？持续了多长时间',
                  answer: '过敏'
                },
                {
                  question: '2.因为什么原因发病？有什么方法能缓解？',
                  answer: '吃海鲜'
                },
                {
                  question: '3.您因为目前的情况去医院做了哪些检查',
                  answer: 'B超'
                }
              ]
            }
          }
        ]
      };
    },
    methods: {
      toggle: _.debounce(function (index) {
        if (this.data[index].isCheck) return;
        let len = this.data.length;
        for (let i = 0; i < len; i++) {
          this.data[i].isCheck = false;
        }
        this.data[index].isCheck = true;
      }, 200)
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../common/stylus/index.styl"
  #VisitsInfo
    background-color:#f5f5f5
    height: 100%
  #tabPanel
    padding: 0 15px
    box-sizing: border-box
    background-color:#f5f5f5
    .item
      width: 100%
      height: auto
      border-left: 1.5px solid #e5e5e5
      padding-top: 23px
      .menutitle
        margin-bottom: 45px
      .menuItem
        display: flex
        flex-direction: row
        align-items: center
        justify-content: space-between
      .menuItem>.da_img
        margin-left: -6px
      .menuItem>.xiao_img
        margin-left: -4.5px
      .date-line
        flex: 1
        font-size: 15px
        color: #5baaec
        margin-left: 16px
      .isCheck
        color: red
      .table
        margin 20px 0 10px 15px
        flex: 1
        font-size: 15px
        color: #333333
    .item .sildermun
      margin-left: 15px
      .sildermun ul li
        height: auto
        list-style: none
  .question
    margin: 15px 0 10px 0
    color: #999999
</style>



