<template>
  <div class="register">
    <calendar class="calendar-wrapper" :on-month-changed="onMonthChanged"></calendar>
    <div class="register-list">
      <div class="register-item" v-for="item in list">
        <div class="register-time">{{item.time}}</div>
        <div class="register-price">{{item.price}}元</div>
        <div class="register-full" v-show="item.full === 0">满号</div>
        <div class="register-push" v-text="item.full === 0 ? '推送特别号' : '推送'"></div>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  import Calendar from '@/components/calendar/Calendar';
  export default {
    name: 'register',
    data() {
      return {
        list: [
          {
            time: '08:00-09:00',
            price: '20',
            full: 0
          },
          {
            time: '09:00-10:00',
            price: '30',
            full: 1
          },
          {
            time: '10:00-11:00',
            price: '25',
            full: 1
          },
          {
            time: '13:00-14:00',
            price: '20',
            full: 1
          },
          {
            time: '14:00-15:00',
            price: '20',
            full: 1
          },
          {
            time: '15:00-16:00',
            price: '22',
            full: 1
          },
          {
            time: '15:00-16:00',
            price: '22',
            full: 1
          },
          {
            time: '15:00-16:00',
            price: '22',
            full: 1
          },
          {
            time: '15:00-16:00',
            price: '22',
            full: 1
          }
        ]
      };
    },
    methods: {
      onMonthChanged(date) {
        window.document.title = date.getFullYear() + '年' + (date.getMonth() + 1) + '月';
      }
    },
    components: {
      [Calendar.name]: Calendar
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/index.styl"
  .register
    display: flex
    flex-direction: column
    .calendar
      flex: 0 0 auto
      background-color: #FFFFFF
    .register-list
      flex: 1 0 0
      margin-top: 15px
      overflow-y: auto
      -webkit-overflow-scrolling : touch
      padding-left: 15px
      padding-right: 15px
      background-color: #FFFFFF
      .register-item
        height: 50px
        display: flex
        align-items: center
        justify-content: space-between
        position: relative
        border-bottom: 1px solid $theme-color-2
        .register-time
          flex: 0 0 auto
          font-size: 14px
          color: $theme-color-1
        .register-price
          font-size: 15px
          color: #FFA801
        .register-full
          position: absolute
          top: 50%
          transform: translatey(-50%)
          right: 77px
          font-size: 14px
          color: #CCCCCC
        .register-push
          flex: 0 0 71px
          height: 25px
          line-height: 25px
          font-size: 13px
          text-align: center
          color: $theme-color-1
          border: 1px solid $theme-color-1
          border-radius: 3px
</style>



