<template>
  <div id="div_followup_detail" v-windowTitle  :data-title="this.projectName">

    <div  v-if="showLoadding"  class="loading">
      <mt-spinner type="triple-bounce" color="#3293BE" style="text-align: center" ></mt-spinner>
    </div>
    <div v-else-if="!isInfoEmpty" class="detailWrapper" ref="detailWrapper" style="overflow-y: auto" >
      <div class="datailContent" >
        <div class="p_item_panel border-1px">
        <p class="p_item">
          <span class="p_item_tilte">审核时间：</span> <span class="p_item_conent"> {{ this.projectDetail.applyTime }}</span>
        </p>
        <p class="line border-1px"></p>
        <p class="p_item">
          <span class="p_item_tilte">管理类型：</span> <span class="p_item_conent"> {{ this.projectDetail.managementType }}</span>
        </p>
          <p class="line border-1px"></p>
        <p class="p_item ">
          <span class="p_item_tilte">随访类型：</span> <span class="p_item_conent"> {{ this.projectDetail.flupType === 1 ? '回顾性': '前瞻性' }}</span>
        </p>
      </div>
        <div class="p_item_panel border-1px">
          <p class="p_item">
            <span class="p_item_tilte">随访项目名称：</span> <span class="p_item_conent"> {{ this.projectDetail.projectName }}</span>
          </p>
          <p class="line border-1px"></p>
          <p class="p_item">
            <span class="p_item_tilte">项目负责人：</span> <span class="p_item_conent"> {{ this.projectDetail.projectLeader }}</span>
          </p>
          <p class="line border-1px"></p>
          <p class="p_item">
            <span class="p_item_tilte">手机号码：</span> <span class="p_item_conent"> {{ this.projectDetail.phoneNumber }}</span>
          </p>
          <p class="line border-1px"></p>
          <p class="p_item">
            <span class="p_item_tilte">邮箱：</span> <span class="p_item_conent"> {{ this.projectDetail.email }}</span>
          </p>
        </div>
        <div class="p_item_panel border-1px">
          <p class="p_item">
            <span class="p_item_tilte">研究病种：</span> <span class="p_item_conent"> {{ this.projectDetail.diseaseType }}</span>
          </p>
          <p class="line border-1px"></p>
          <p class="p_item">
            <span class="p_item_tilte">所需病案数：</span> <span class="p_item_conent"> {{ this.projectDetail.caseNumber }}</span>
          </p>
          <p class="line border-1px"></p>
          <p class="p_item">
            <span class="p_item_tilte">项目状态：</span> <span   style="color:#5baaec"> {{ this.projectDetail.status === 1 ? '待审核' : (this.projectDetail.status === 2 ? '通过' : '审核未通过' ) }}</span>
          </p>
        </div>
        <div class="p_item_panel border-1px">
          <p class="p_item" @click="toBasisInfo({url: '/projectteaminfo'})">
            <span class="p_item_tilte">项目成员</span>
            <img class="more_icon" src="~assets/item_more_icon.png"   width="10px" height="20px"/>
          </p>
          <p class="line border-1px"></p>
          <p class="p_item" @click="toBasisInfo({url: '/databasefield'})">
            <span class="p_item_tilte">提取数据字段</span>
            <img class="more_icon" src="~assets/item_more_icon.png"   width="10px" height="20px"/>
          </p>
          <p class="line border-1px"></p>
          <p class="p_item" @click="toBasisInfo({url: '/filterinfo'})">
            <span class="p_item_tilte">条件筛选</span>
            <img class="more_icon" src="~assets/item_more_icon.png"   width="10px" height="20px"/>
          </p>
        </div>
        <div class="p_item_panel border-1px">
          <p class="p_item_bottom">
            <span class="item_bottom_title" >随访目的：</span> <span class="p_item_conent item_bottom_conent">{{this.projectDetail.flupPurpose}}</span>
          </p>
          <p class="line border-1px"></p>
          <p class="p_item_bottom">
            <span class="p_item_tilte item_bottom_title">研究目的：</span> <span class="p_item_conent item_bottom_conent"> {{this.projectDetail.researchPurpose}}</span>
          </p>
          <p class="line border-1px"></p>
          <p class="p_item_bottom">
            <span class="p_item_tilte item_bottom_title">文字备注：</span> <span  class="remark_conent"  :style="{color:( this.isRemarksEmpty(this.projectDetail.remarks)?'#F8829A':'#333333') }"> {{this.projectDetail.remarks}}</span>
          </p>
          <p class="line border-1px"></p>
          <p class="p_item_bottom">
            <span class="p_item_tilte item_bottom_title">审核意见：</span> <span class="item_bottom_conent" style="color:#5baaec"> {{this.projectDetail.auditOpinion}}</span>
          </p>
        </div>
      </div>
    </div>
    <div class="empty_data"  v-else>
      暂无数据
    </div>
  </div>
</template>
<script>
  import { Spinner, MessageBox } from 'mint-ui';
  import { netWorkPost } from '../common/js/netjob';
  export default {
      name: 'FollowupDetail',
      components: {
       'mt-spinner': Spinner
      },
      methods: {
        toBasisInfo: function (pam) {
          this.$router.push({ path: pam.url, query: { projectId: this.projectID, doctorId: 1 } });
        },
        loadProjectDetail: function (val) {
          let vm = this;
          let pam = {
            url: 'project/projectDetail',
            data: {},
            before: function () {
              vm.showLoadding = true;
            },
            success: function (res) {
              let response = res.data;
              if (response.status > 0) {
                this.fail(res);
                return;
              }
              vm.projectDetail = response.data.list;
            },
            fail: function (res) {
              MessageBox.alert('加载失败!', '提示');
            },
            after: function () {
              vm.showLoadding = false;
            }
          };
          Object.assign(pam, val);
          pam.before();
          netWorkPost(pam);
        },
        isRemarksEmpty: function (val) {
            return !val;
        }
      },
      created: function () {
        this.projectName = this.$route.params.projectName;
        // 初始化项目详情
        let vm = this;
        // vm.projectID = this.$route.params.projectID;
        vm.projectID = this.$route.params.projectID;
        let doctorId = this.$route.params.doctorId;
        this.loadProjectDetail({
          data: {
            doctorId: doctorId,
            projectId: vm.projectID
          }
        });
      },
      updated: function () {
     //   this._initConentHeight();
      //  this._initBScroll();
      },
      computed: {
        isInfoEmpty: function () {
            return JSON.stringify(this.projectDetail) === '{}';
        }
      },
      data () {
          return {
             projectID: '',
             showLoadding: false,
             wrapperHeight: 0,
             projectName: '',
             projectDetail: {}
          };
      }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  @import "../common/stylus/mixin.styl";
  #div_followup_detail
    background-color:#e5e5e5
    padding-top:15px
    padding-bottom:30px
    .loading
      margin-top:10px
    .p_item_panel
      background-color:white
      margin-bottom:15px
      border-top-bottom-1px(#e5e5e5)
      .p_item
        height:45px
        line-height:45px
        padding:0px 15px
        font-size:15px
        color:#666
        .more_icon
          float:right
          margin-top:10px
        .p_item_conent
          color:#333333
      .p_item_bottom
        display:flex
        padding:15px 15px
        font-size:15px
        color:#666
        .item_bottom_title
           width:80px
        .item_bottom_conent
           flex:1
           color:#333333
      .line
        position:relative
        border-bottom-1px(#e5e5e5)
    .empty_data
      text-align:center;
      width:100%
      padding-top:20px
</style>



