<template >
  <div id="div_applyinfo"  v-windowTitle  data-title="我的申请" >
    <t-tabbar :tabItems="this.tabItems" @introduce="tabbarItemClick"></t-tabbar>
    <div  v-if="showLoadding"  class="loading">
    <mt-spinner type="triple-bounce" color="#3293BE" ></mt-spinner>
    </div>
    <scroller  v-if="pListData.length>0" :on-refresh="refresh" :on-infinite="infinite" ref="myscroller" style="margin-top:47px">
       <div class="p_list">
          <p class="p_list_item border-1px" v-for="(item,index) in this.pListData" @click="toDetail(item)">
            <span class="pname">{{item.project}}</span>
            <span class="ptiem">{{item.date}}</span>
          </p>
       </div>
    </scroller>
    <div class="empty_data"  v-else>
      暂无数据
    </div>
  </div>
</template>
<script>
  import { Spinner } from 'mint-ui';
  import MyTabbar from './Tabbar/MyTabbar.vue';
  import { netWorkPost } from '../common/js/netjob';
  export default {
    name: 'ApplyInfo',
    components: {
      't-tabbar': MyTabbar,
      'mt-spinner': Spinner
    },
    methods: {
      tabbarItemClick: function (item) {
        this.status = item.status; // 改变状态值
      },
      toDetail: function (item) {
        this.$router.push({ name: 'FollowupDetail', params: { doctorId: 1, projectID: item.id, projectName: item.project } });
      },
      refresh: function (done) {
        let vm = this;
        vm.pageNumber = 1;
        this.initProjectList({
          data: {
            'pageNumber': vm.pageNumber
          },
          complete: function () {
            done(true);
          }
        });
      },
      infinite: function (done) {
        let vm = this;
        if (vm.toatalPage > vm.pageNumber) {
          vm.pageNumber++;
          this.initProjectList({
            data: {
              'pageNumber': vm.pageNumber
            },
            complete: function () {
              done();
            }
          });
        } else {
          done(true);
        }
        console.log('toatalPage' + vm.toatalPage);
        console.log('pageNumber' + vm.pageNumber);
      },
      initProjectList: function (val) {
        let vm = this;
        let doctorID = this.$route.query.doctorid;
        let requestData = {
          'doctorId': doctorID,
          'pageNumber': vm.pageNumber,
          'pageSize': 20,
          'status': vm.status
        };
        if (val) {
          Object.assign(val.data, requestData);
        }
        let pam = {
          data: {},
          complete: function () {
          },
          success: function (res) {
            let response = res.data;
            if (response.status > 0) return;
            if (this.data.pageNumber === 1) {  // 判断是加载第一页
              vm.toatalPage = response.data.total;
              vm.pListData = response.data.list;
            } else {  // 判断是否加载更多
              let moreData = response.data.list;
              moreData.forEach(function (item) {
                vm.pListData.push(item);
              });
              console.log(vm.pListData);
            }
            this.complete();
          },
          fail: function (res) {
          }
        };
        pam.data = requestData;
        Object.assign(pam, val);
        this.requestProjectList(pam);
      },
      requestProjectList: function (val) {
        let vm = this;
        let pam = {
          url: 'followup/myApplicationList',
          data: {},
          type: '',
          before: function () {
            vm.showLoadding = true;
          },
          success: function (res) {
          },
          fail: function (res) {
          },
          after: function () {
            vm.showLoadding = false;
          }
        };
        Object.assign(pam, val);
        pam.before();
        netWorkPost(pam);
      }
    },
    watch: {
      status: function (newVule) {
        this.pageNumber = 1;
        this.initProjectList({ data: {
          'pageNumber': this.pageNumber
        }});
      }
    },
    mounted: function() {
      this.pageNumber = 1;
      this.initProjectList({ data: {
        'pageNumber': this.pageNumber
      }});
    },
     data () {
       return {
           tabItems: [
             { title: '待审核', status: 1, isCheck: true },
             { title: '通过', status: 2, isCheck: false },
             { title: '不通过', status: 3, isCheck: false }
           ],
          pListData: [],
          showLoadding: true,
          toatalPage: 1, // 总分页大小
          status: 1,  // 审核状态
          pageNumber: 1 // 每加载一次增加1，刷新时默认为1
       };
     }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  @import "../common/stylus/mixin.styl";
  #div_applyinfo
    background-color:#e5e5e5
    height:100%
    .loading
       display:flex
       z-index:10
       position:absolute;
       height:100%
       width:100%
       top:0px
       left:0px
       align-items:center
       justify-content:center
    .p_list
      .p_list_item
        background-color:white
        display:flex
        align-items:center
        height:48px
        padding:0px 15px
        font-size:15px
        color:#333333
        border-bottom-1px(#e5e5e5)
        .pname
          flex:1
          text-align:left
        .ptiem
          flex:1
          text-align:right
    .empty_data
      text-align:center;
      width:100%
      padding-top:20px
</style>



