<template>
  <div id="otherCheckResult">
    <div class="mask" @click="SearchTo()"></div>
    <mt-search class="searchBar"></mt-search>
    <div class="tabbar" v-model="selected">
      <span class="tabbarItem left" :class="selected==='ck' ? 'itemSelected' : 'itemUnselected'" @click="tabbarChange('ck')">查看</span>
      <span class="tabbarItem center" :class="selected==='xdt' ? 'itemSelected' : 'itemUnselected'" @click="tabbarChange('xdt')">心电图</span>
      <span class="tabbarItem right" :class="selected==='alo' ? 'itemSelected' : 'itemUnselected'" @click="tabbarChange('alo')">ALO</span>
    </div>
    <!-- tab-container -->
    <mt-tab-container v-model="selected" class="tabContainer">
      <mt-tab-container-item id="ck" class="ckItem">
        <div class="resultList">
          <div v-for="item in results">
            <div class="result">
              <div class="resultItem">
                <span class="itemName">检查申请单号:</span>
                <span class="itemDetail">{{item.applyId}}</span>
              </div>
              <div class="resultItem">
                <span class="itemName">检查类型:</span>
                <span class="itemDetail">{{item.checkType}}</span>
              </div>
              <div class="resultItem">
                <span class="itemName">检查部位或类型:</span>
                <span class="itemDetail">{{item.partType}}</span>
              </div>
              <div class="resultItem">
                <span class="itemName">申请医生:</span>
                <span class="itemDetail">{{item.applyDoctor}}</span>
              </div>
              <div class="resultItem">
                <span class="itemName">申请科室:</span>
                <span class="itemDetail">{{item.applyPartment}}</span>
              </div>
              <div class="resultItem">
                <span class="itemName">检查科室:</span>
                <span class="itemDetail">{{item.checkPartment}}</span>
              </div>
              <div class="resultItem">
                <span class="itemName">报告时间:</span>
                <span class="itemDetail">{{item.reportDate}}</span>
              </div>
              <div class="goDetail" @click="getDetail(item)">
                <span class="detailText">详情</span>
                <img src="../assets/item_more_icon.png" class="detailIcon">
              </div>
            </div>
          </div>
        </div>
      </mt-tab-container-item>
      <mt-tab-container-item id="xdt">
        <!--<mt-cell v-for="n in 4" :title="'测试 ' + n" />-->
      </mt-tab-container-item>
      <mt-tab-container-item id="alo">
        <!--<mt-cell v-for="n in 6" :title="'选项 ' + n" />-->
      </mt-tab-container-item>
    </mt-tab-container>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'OtherCheckResult',
  data() {
      return {
        active: 'tab-container1',
        selected: 'ck',
        results: [
          {
            applyId: 123456,
            checkType: '检查类型',
            partType: '检查部位',
            applyDoctor: '医生姓名',
            applyPartment: '科室名',
            checkPartment: '检查科室',
            reportDate: '报告时间'
          }
        ]
      };
  },
  methods: {
      tabbarChange: function (title) {
         if (this.selected === title) {
           return;
         }
         this.selected = title;
      },
      getDetail: function (item) {
        this.$router.push('/imageresultdetail');
      },
      SearchTo: function () {
        this.$router.push('/searchinfo');
      }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
  #otherCheckResult
    .mask
      width: 90%
      height: 52px
      position: fixed
      z-index: 9999
      top: 50px
    .searchBar
      height: 52px
      top: 0px
      width 100%
      .mint-searchbar
        background-color #e5e5e5
        .mintui-search
          font-size: 20px
        .mint-searchbar-core
          color: #333333
          padding-left: 5px
    .tabbar
      background-color white
      height 50px
      display flex
      justify-content center
      .tabbarItem
        display inline-block
        width 75px
        height 25px
        line-height 25px
        font-size 13px
        text-align center
        margin-top 10px
      .left
        border 2px solid #5baaec
        border-radius 5px 0 0 5px
      .right
        border 2px solid #5baaec
        border-radius 0 5px 5px 0
      .center
        border-top 2px solid #5baaec
        border-bottom 2px solid #5baaec
      .itemSelected
        background-color #5baaec
        color white
      .itemUnselected
        background-color white
        color #5baaec
    .tabContainer
      .ckItem
        .resultList
          background-color #e5e5e5
          .result
            background-color #ffffff
            margin-top 10px
            .resultItem
              padding 15px 15px 0
              .itemName
                font-size 15px
                color: #666666
              .itemDetail
                font-size 15px
                color: #333333
            .goDetail
              display flex
              height 50px
              margin-top 15px
              border-top 1px solid #e5e5e5
              border-bottom 1px solid #e5e5e5
              .detailText
                font-size 15px
                color #5baaec
                flex auto
                padding 16px
              .detailIcon
                width 8px
                height 16px
                flex 0
                padding 16px
</style>



