import axios from 'axios';
export function netWorkPost (pam) {
    axios.post(pam.url, pam.data)
    .then((res) => {
      pam.success(res);
      pam.after();
    })
    .catch((res) => {
      console.log(res);
      pam.fail(res);
      pam.after();
    });
};



