<template>
  <div id="doctorAdvice">
    <div class="tabbar" v-model="selected">
      <span class="tabbarItem left" :class="selected==='cqyz' ? 'itemSelected' : 'itemUnselected'" @click="tabbarChange('cqyz')">长期医嘱</span>
      <span class="tabbarItem center" :class="selected==='lsyz' ? 'itemSelected' : 'itemUnselected'" @click="tabbarChange('lsyz')">临时医嘱</span>
      <span class="tabbarItem right" :class="selected==='cyyz' ? 'itemSelected' : 'itemUnselected'" @click="tabbarChange('cyyz')">出院医嘱</span>
    </div>
    <p class="protocol">
        <span class="protocl_check" :class="[this.isCheck ? 'on_check':'off_check']"><input type="checkbox" v-model="isCheck" id="ck_protocol"/> </span>
      <span>显示已停止医嘱</span>
    </p>
    <mt-tab-container v-model="selected" class="tabContainer">
      <mt-tab-container-item id="cqyz" class="cqyzItem">
        <div class="resultList">
          <div v-for="item in cqyzResult">
            <div class="result">
              <div class="resultItem">
                <span class="itemName">长期医嘱:</span>
                <span class="itemDetail">{{item.yzType}}</span>
              </div>
              <div class="resultItem">
                <span class="itemName">每次用量:</span>
                <span class="itemDetail">{{item.dosage}}</span>
              </div>
              <div class="resultItem">
                <span class="itemName">频率:</span>
                <span class="itemDetail">{{item.frequency}}</span>
              </div>
              <div class="resultItem">
                <span class="itemName">用药途径:</span>
                <span class="itemDetail">{{item.approach}}</span>
              </div>
              <div class="resultItem">
                <span class="itemName">起始时间:</span>
                <span class="itemDetail">{{item.startDate}}</span>
              </div>
              <div class="resultItem">
                <span class="itemName">停止时间:</span>
                <span class="itemDetail">{{item.endDate}}</span>
              </div>
              <div class="resultItem lastItem">
                <span class="itemName">备注:</span>
                <span class="itemDetail">{{item.remark}}</span>
              </div>
            </div>
          </div>
        </div>
      </mt-tab-container-item>
      <mt-tab-container-item id="lsyz" class="lsyzItem">
        <div class="resultList">
          <div v-for="item in lsyzResult">
            <div class="result">
              <div class="resultItem">
                <span class="itemName">临时医嘱:</span>
                <span class="itemDetail">{{item.yzType}}</span>
              </div>
              <div class="resultItem">
                <span class="itemName">每次用量:</span>
                <span class="itemDetail">{{item.dosage}}</span>
              </div>
              <div class="resultItem">
                <span class="itemName">频率:</span>
                <span class="itemDetail">{{item.frequency}}</span>
              </div>
              <div class="resultItem">
                <span class="itemName">用药途径:</span>
                <span class="itemDetail">{{item.approach}}</span>
              </div>
              <div class="resultItem">
                <span class="itemName">起始时间:</span>
                <span class="itemDetail">{{item.startDate}}</span>
              </div>
              <div class="resultItem">
                <span class="itemName">停止时间:</span>
                <span class="itemDetail">{{item.endDate}}</span>
              </div>
              <div class="resultItem lastItem">
                <span class="itemName">备注:</span>
                <span class="itemDetail">{{item.remark}}</span>
              </div>
            </div>
          </div>
        </div>
      </mt-tab-container-item>
      <mt-tab-container-item id="cyyz" class="cyyzItem">
        <div class="resultList">
          <div v-for="item in cyyzResult">
            <div class="result">
              <div class="resultItem">
                <span class="itemName">出院医嘱:</span>
                <span class="itemDetail">{{item.yzType}}</span>
              </div>
              <div class="resultItem">
                <span class="itemName">每次用量:</span>
                <span class="itemDetail">{{item.dosage}}</span>
              </div>
              <div class="resultItem">
                <span class="itemName">频率:</span>
                <span class="itemDetail">{{item.frequency}}</span>
              </div>
              <div class="resultItem">
                <span class="itemName">用药途径:</span>
                <span class="itemDetail">{{item.approach}}</span>
              </div>
              <div class="resultItem">
                <span class="itemName">起始时间:</span>
                <span class="itemDetail">{{item.startDate}}</span>
              </div>
              <div class="resultItem">
                <span class="itemName">停止时间:</span>
                <span class="itemDetail">{{item.endDate}}</span>
              </div>
              <div class="resultItem lastItem">
                <span class="itemName">备注:</span>
                <span class="itemDetail">{{item.remark}}</span>
              </div>
            </div>
          </div>
        </div>
      </mt-tab-container-item>
    </mt-tab-container>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'DoctorAdvice',
  data() {
      return {
          isCheck: false,
          selected: 'cqyz',
          cqyzResult: [
            {
              yzType: '药物名称',
              dosage: '用量',
              frequency: '服用频率',
              approach: '用药途径',
              startDate: '起始用药时间',
              endDate: '结束用药时间',
              remark: '备注内容'
            }
          ],
          lsyzResult: [
            {
              yzType: '药物名称',
              dosage: '用量',
              frequency: '服用频率',
              approach: '用药途径',
              startDate: '起始用药时间',
              endDate: '结束用药时间',
              remark: '备注内容'
            },
            {
              yzType: '药物名称',
              dosage: '用量',
              frequency: '服用频率',
              approach: '用药途径',
              startDate: '起始用药时间',
              endDate: '结束用药时间',
              remark: '备注内容'
            }
          ],
          cyyzResult: [
            {
              yzType: '药物名称',
              dosage: '用量',
              frequency: '服用频率',
              approach: '用药途径',
              startDate: '起始用药时间',
              endDate: '结束用药时间',
              remark: '备注内容'
            }
          ]
      };
  },
  methods: {
    tabbarChange: function (title) {
      if (this.selected === title) {
        return;
      }
      this.selected = title;
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
  #doctorAdvice
    .tabbar
      background-color white
      height 50px
      display flex
      justify-content center
      position relative
      top: 15px
      .tabbarItem
        display inline-block
        width 75px
        height 25px
        line-height 25px
        font-size 13px
        text-align center
        margin-top 10px
      .left
        border 2px solid #5baaec
        border-radius 5px 0 0 5px
      .right
        border 2px solid #5baaec
        border-radius 0 5px 5px 0
      .center
        border-top 2px solid #5baaec
        border-bottom 2px solid #5baaec
      .itemSelected
        background-color #5baaec
        color white
      .itemUnselected
        background-color white
        color #5baaec
    .protocol
      padding: 15px 10px 0px
      height: 20px
      line-height: 30px
      font-size: 14px
      color: #999
      .on_check
        background-image: url('~assets/xuanzhong@2x.png')
      .off_check
        background-image: url('~assets/weixuanzhong@2x.png')
      .protocl_check
        cursor: pointer
        text-align: center
        width: 14px
        height: 14px
        background-size: 14px 14px
        background-repeat: no-repeat
        margin-right: 4px
        vertical-align: sub
        #ck_protocol
          font-size: 0px
          width: 14px
          height: 14px
          opacity: 0
          cursor: pointer
    .tabContainer
      .cqyzItem
        .resultList
          background-color #e5e5e5
          .result
            background-color #ffffff
            margin-top 10px
            .resultItem
              padding 15px 15px 0
              .itemName
                font-size 15px
                color: #666666
              .itemDetail
                font-size 15px
                color: #333333
                padding-left 5px
            .lastItem
              padding-bottom 15px
      .lsyzItem
        .resultList
          background-color #e5e5e5
          .result
            background-color #ffffff
            margin-top 10px
            .resultItem
              padding 15px 15px 0
              .itemName
                font-size 15px
                color: #666666
              .itemDetail
                font-size 15px
                color: #333333
                padding-left 5px
            .lastItem
              padding-bottom 15px
      .cyyzItem
        .resultList
          background-color #e5e5e5
          .result
            background-color #ffffff
            margin-top 10px
            .resultItem
              padding 15px 15px 0
              .itemName
                font-size 15px
                color: #666666
              .itemDetail
                font-size 15px
                color: #333333
                padding-left 5px
            .lastItem
              padding-bottom 15px
</style>



