<template>
  <div id="imageResult">
    <div class="mask" @click="SearchTo()"></div>
    <mt-search v-model="value" class="searchBar"></mt-search>
    <div class="resultList">
      <div v-for="item in results">
        <div class="result">
          <div class="resultItem">
            <span class="itemName">检查申请单号:</span>
            <span class="itemDetail">{{item.applyId}}</span>
          </div>
          <div class="resultItem">
            <span class="itemName">检查类型:</span>
            <span class="itemDetail">{{item.checkType}}</span>
          </div>
          <div class="resultItem">
            <span class="itemName">检查部位或类型:</span>
            <span class="itemDetail">{{item.partType}}</span>
          </div>
          <div class="resultItem">
            <span class="itemName">申请医生:</span>
            <span class="itemDetail">{{item.applyDoctor}}</span>
          </div>
          <div class="resultItem">
            <span class="itemName">申请科室:</span>
            <span class="itemDetail">{{item.applyPartment}}</span>
          </div>
          <div class="resultItem">
            <span class="itemName">检查科室:</span>
            <span class="itemDetail">{{item.checkPartment}}</span>
          </div>
          <div class="resultItem">
            <span class="itemName">报告时间:</span>
            <span class="itemDetail">{{item.reportDate}}</span>
          </div>
          <div class="goDetail" @click="goDetail(item)">
            <span class="detailText">详情</span>
            <img src="../assets/item_more_icon.png" class="detailIcon">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
  name: 'ImageResult',
  data() {
       return {
          value: '',
          results: [
            {
                applyId: 123456,
                checkType: '检查类型',
                partType: '检查部位',
                applyDoctor: '医生姓名',
                applyPartment: '科室名',
                checkPartment: '检查科室',
                reportDate: '报告时间'
            },
            {
              applyId: 123456,
              checkType: '检查类型',
              partType: '检查部位',
              applyDoctor: '医生姓名',
              applyPartment: '科室名',
              checkPartment: '检查科室',
              reportDate: '报告时间'
            }
          ]
       };
   },
  methods: {
       goDetail: function (item) {
           this.$router.push({path: '/imageresultdetail'});
       },
        SearchTo: function () {
          this.$router.push('/searchinfo');
        }
   }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
  #imageResult
    .mask
      width: 90%
      height: 52px
      position: fixed
      z-index: 9999
      top: 50px
    .searchBar
      height: 52px
      /*position fixed*/
      top: 0px
      width 100%
      .mint-searchbar
        background-color #e5e5e5
        .mintui-search
          font-size: 20px
        .mint-searchbar-core
          color: #333333
          padding-left: 5px
    .resultList
      background-color #e5e5e5
      .result
        background-color #ffffff
        margin-top 10px
        .resultItem
          padding 15px 15px 0
          .itemName
            font-size 15px
            color: #666666
          .itemDetail
            font-size 15px
            color: #333333
        .goDetail
          display flex
          height 50px
          margin-top 15px
          border-top 1px solid #e5e5e5
          border-bottom 1px solid #e5e5e5
          .detailText
            font-size 15px
            color #5baaec
            flex auto
            padding 16px
          .detailIcon
            width 8px
            height 16px
            flex 0
            padding 16px
</style>



