<template>
  <div id="CheckResult">
    <div class="mask" @click="SearchTo()"></div>
    <mt-search class="searchBar"></mt-search>
    <div class="detail" v-for="(item,index) in this.list">
      <ul>
        <li>检查编号：<span>{{item.code}}</span></li>
        <li>项目组合：<span>{{item.project}}</span></li>
        <li>申请医生：<span>{{item.doctor}}</span></li>
        <li>申请科室：<span>{{item.department}}</span></li>
        <li>报告时间：<span>{{item.date}}</span></li>
      </ul>
      <div class="result" @click="getResult(item)">
        <span>检查结果</span>
        <span class="icon-more" ></span>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    components: {
    },
    data() {
      return {
        list: [
          {
            code: '12321421435',
            project: '性病三项',
            doctor: '李伟奇',
            department: '耳鼻喉一区',
            date: '2017-11-23'
          },
          {
            code: '12321421435',
            project: '性病三项',
            doctor: '李伟奇',
            department: '耳鼻喉一区',
            date: '2017-11-23'
          }
        ]
      };
    },
    methods: {
        getResult: function (item) {
          this.$router.push('/examresults');
        },
        SearchTo: function () {
          this.$router.push('/searchinfo');
        }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus" >
  @import "../common/stylus/index.styl";
  #CheckResult
    height: 100%
    background-color: #f5f5f5
  .mask
    width: 90%
    height: 52px
    position: fixed
    z-index: 9999
    top: 50px
  .searchBar
    height: 52px
    top: 0px
    width 100%
    .mint-searchbar
      background-color #e5e5e5
      .mintui-search
        font-size: 20px
      .mint-searchbar-core
        color: #333333
        padding-left: 5px
  .detail
    height: auto
    margin-bottom: 15px
  .detail ul
    background-color: #ffffff
    padding: 15px 0
    box-sizing: border-box
  .detail ul li
    list-style: none
    margin-bottom: 20px
    color: #666666
  .detail ul li:last-child
    margin-bottom: 0
  .detail ul li span
    color: #333333
  .detail .result
    display: flex
    flex-direction: row
    align-items: center
    justify-content: space-between
    height: 50px
    padding: 0 15px
    margin-top: 1px
    background-color: #ffffff
  .result span:first-child
    color: #5baaec
  .result .icon-more
    width: 8px
    height: 14px
    display: inline-block
    background: url("../assets/More@2x.png") no-repeat
    background-size: 8px 14px






</style>



