<template>
  <div class="calendar">
    <div class="weekday-wrapper">
      <div class="weekday" v-for="item in weekdays">{{item.name}}</div>
    </div>
    <swiper :options="swiperOption" ref="swiper">
      <swiper-slide v-for="(month, index) in months" :key="index">
        <table>
          <tr v-for="week in month.weeks">
            <!--如果日期的毫秒差少于半天，就可以认为是同一天-->
            <td v-for="date in week" @click="selectedDate = date"><div :class="[{current: Math.abs(date.getTime() - currDate.getTime()) < 43200000},{selected: Math.abs(date.getTime() - selectedDate.getTime()) < 4320000},{currMonth: (date.getMonth() === month.date.getMonth())}]">{{date.getDate()}}</div></td>
          </tr>
        </table>
      </swiper-slide>
    </swiper>
  </div>
</template>
<script type="text/ecmascript-6">
  import {swiper, swiperSlide} from 'vue-awesome-swiper';
  export default {
    name: 'calendar',
    data() {
      return {
        weekdays: [
          {
            name: '周一',
            index: 1
          },
          {
            name: '周二',
            index: 2
          },
          {
            name: '周三',
            index: 3
          },
          {
            name: '周四',
            index: 4
          },
          {
            name: '周五',
            index: 5
          },
          {
            name: '周六',
            index: 6
          },
          {
            name: '周日',
            index: 0
          }
        ],
        currDate: new Date(),
        selectedDate: new Date(),
        currSlideIndex: 1,
        months: [
          {
            date: new Date(),
            weeks: [[new Date()]]
          },
          {
            date: new Date(),
            weeks: [[new Date()]]
          },
          {
            date: new Date(),
            weeks: [[new Date()]]
          }
        ],
        swiperOption: {
          notNextTick: true,
          autoHeight: true,
          initialSlide: 1,
          on: {
            slideNextTransitionEnd: () => {
              this.$nextTick(() => {
                let length = this.months.length;
                let middleIndex = Math.floor(length / 2);
                if (middleIndex === this.swiper.realIndex) {
                  return;
                }
                this.onSlideChanged();
                let slide = this.swiper.slides[0];
                this.swiper.removeSlide(0);
                let month = slide.month;
                month.date.setMonth(month.date.getMonth() + length);
                this.generateDateList(month);
                this.swiper.appendSlide(slide);
              });
            },
            slidePrevTransitionEnd: () => {
              this.$nextTick(() => {
                let length = this.months.length;
                let middleIndex = Math.floor(length / 2);
                if (middleIndex === this.swiper.realIndex) {
                  return;
                }
                this.onSlideChanged();
                let slide = this.swiper.slides[length - 1];
                this.swiper.removeSlide(length - 1);
                let month = slide.month;
                month.date.setMonth(month.date.getMonth() - length);
                this.generateDateList(month);
                this.swiper.prependSlide(slide);
              });
            }
          }
        }
      };
    },
    props: {
      onMonthChanged: {
        type: Function,
        default: null
      }
    },
    components: {
      swiper, swiperSlide
    },
    created() {
      let month = this.months[1];
      month.date.setDate(1);
      this.generateDateList(month);
      month = this.months[0];
      let date = month.date;
      date.setMonth(date.getMonth() - 1);
      date.setDate(1);
      this.generateDateList(month);
      month = this.months[2];
      date = month.date;
      date.setMonth(date.getMonth() + 1);
      date.setDate(1);
      this.generateDateList(month);
    },
    methods: {
      generateDateList(month) {
        // 将参考的date变成该月第一天
        let weeks = month.weeks;
        weeks.splice(0, weeks.length);
        // 一天的毫秒数24 * 60 * 60 * 1000
        let milliseconds = 86400000;
        // 获取下一月份
        let nextMonth = (month.date.getMonth() + 1) % 12;
        // 整个数组的第一天
        let firstDate = new Date(month.date.getTime());
        // 获取本月第一天是星期几
        let weekday = firstDate.getDay();
        if (weekday === 0) {
          weekday = 7;
        }
        firstDate.setDate(firstDate.getDate() - weekday);
        let timestamp = firstDate.getTime();
        let i = 0;
        let date;
        let week;
        while (true) {
          timestamp += milliseconds;
          date = new Date(timestamp);
          if (date.getMonth() === nextMonth && week.length > 6) {
            break;
          }
          if (i % 7 === 0) {
            week = [];
            weeks.push(week);
          }
          week.push(date);
          ++i;
        }
      },
      onSlideChanged() {
        let date = this.swiper.slides[this.swiper.realIndex].month.date;
        if (this.onMonthChanged != null) {
          this.onMonthChanged(date);
        }
      }
    },
    mounted() {
      this.swiper.slides[0].month = this.months[0];
      this.swiper.slides[1].month = this.months[1];
      this.swiper.slides[2].month = this.months[2];
      if (this.onMonthChanged != null) {
        let date = new Date();
        date.setDate(1);
        this.onMonthChanged(date);
      }
    },
    computed: {
      swiper() {
        return this.$refs.swiper.swiper;
      }
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/index.styl"
  .calendar
    width: 100%
    .weekday-wrapper
      box-sizing border-box
      width: 100%
      padding-left: 12px
      padding-right: 12px
      display: flex
      border-bottom: 1px solid $theme-color-2
      .weekday
        flex: 1 0 0
        height: 50px
        line-height: 50px
        text-align: center
        font-size: 15px
        color: #333333
    .swiper-slide
      box-sizing border-box
      padding-left: 12px
      padding-right: 12px
      table
        box-sizing: border-box
        table-layout: fixed
        height: 245px
        width: 100%
        td
          position: relative
          div
            position: absolute
            top: 50%
            left: 50%
            transform: translate(-50%, -50%)
            box-sizing: border-box
            background-origin border-box
            vertical-align: middle
            text-align: center
            font-size: 13px
            border-radius: 50%
            width: 30px
            height: 30px
            line-height: 30px
            color: #CCCCCC
            border: 1px solid transparent
            &.currMonth
              color: #333333
            &.current
              color: $theme-color-1
              border-color: $theme-color-1
            &.selected
              color: #FFFFFF
              background-color: $theme-color-1

</style>



