<template>
  <div id="CourseInfo">
    <div class="courseinfo">
      <!--分类-->
      <div class="cate">
        <ul v-bind="{style:'width:'+ulWidth+'px'}">
          <li v-for="(item,index) in this.cates" :class="{selected: index===currentIndex}"  @click="ItemClick(index)">{{item}}</li>
        </ul>
      </div>
      <div class="date">
        <span>首次病程记录</span>
        <p><span>2017-11-20</span><span>14:42</span></p>
      </div>
      <div class="description">
        {{this.data.shouci}}
      </div>
      <div class="content">
          <div class="detail" v-for="(item,index) in this.data.content">
            <div class="detail-title">{{item.title}}</div>
            <div  class="detail-list" v-for="(v,index) in item.detail">{{v}}</div>
          </div>
      </div>
    </div>
  </div>
</template>
<script>
    export default{
      name: 'CourseInfo',
      components: {
      },
      data() {
        return {
            currentIndex: 0,
            cates: ['16/4/9', '16/4/8', '16/4/7', '17/4/6', '16/4/9', '16/4/8', '16/4/7', '17/4/6'], // 用来存储看病日期数据的数组
            data: {
              name: '李凯',
              sex: 1,
              bed: 5,
              keshi: '精神科',
              zhuyuanhao: '32524',
              shouci: '患者对声反应差伴学语异常4年，于2017-11-12 9:00入院',
              content: [
                {
                  title: '（一）病例特点',
                  detail: [
                    '1、青年女患者。',
                    '2、患者出生后未进行听力检查和筛选，患儿一直对声音反应差，仅能发出简单的字的读音。',
                    '3、患者出生后未进行听力检查和筛选，患儿一直对声音反应差，仅能发出简单的字的读音。'
                  ]
                },
                {
                  title: '（二）诊断依据及鉴别诊断',
                  detail: [
                    '1、青年女患者。',
                    '2、患者出生后未进行听力检查和筛选，患儿一直对声音反应差，仅能发出简单的字的读音。',
                    '3、患者出生后未进行听力检查和筛选，患儿一直对声音反应差，仅能发出简单的字的读音。'
                  ]
                },
                {
                  title: '（三）诊疗计划',
                  detail: [
                    '1、青年女患者。',
                    '2、患者出生后未进行听力检查和筛选，患儿一直对声音反应差，仅能发出简单的字的读音。',
                    '3、患者出生后未进行听力检查和筛选，患儿一直对声音反应差，仅能发出简单的字的读音。'
                  ]
                }
              ]
            }
        };
      },
      created() {
        // 请求数据方法
        this.getContent();
        // 获取日期数据分类
        this.getcates();
      },
      methods: {
        ItemClick(index) {
          this.currentIndex = index;
        },
        getcates() {
          // 实现当前日期分类ul的总宽度=日期个数*每个日期宽度
          let w = 60;
          let count = this.cates.length;
          this.ulWidth = w * count;
        },
        getContent() {
        }
      }
    };
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../common/stylus/index.styl"
  #CourseInfo
    background-color: #f5f5f5
    padding-top:15px
    padding-bottom: 15px
  .courseinfo
    height: auto
  .cate
    height: 50px
    background-color: #ffffff
    overflow-x: auto
    margin: 0 0 15px 0
    .selected
      color: #333
  .cate ul
    padding-left: 15px
    display: flex
    flex-direction: row
  .cate ul li
    list-style: none
    display: inline-block
    text-align: center
    line-height: 50px
    margin-right: 35px
    color: #999999
  .date
    height: 50px
    background-color: #ffffff
    display: flex
    flex-direction: row
    justify-content: space-between
    align-items: center
    padding-left: 15px
    padding-right: 15px
    margin-bottom: 1px
    &>span
      color: #333333
      font-weight: bold
    &>p
      color: #999999
      font-size: 12px
      &>span:first-child
        margin-right: 9px
  .description
    display: flex
    align-items center
    height: 70px
    line-height: 24px
    background-color: #ffffff
    padding-left: 15px
    padding-right: 15px
    text-indent: 18px
    font-size: 15px
    color: #333333
    margin-bottom: 1px
  .content
    padding: 0
    box-sizing: border-box
    background-color: #f5f5f5
  .content .detail
    background-color: #ffffff
    margin-bottom: 1px
    padding: 17px 15px 15px 15px
  .detail:last-child
    margin-bottom: 0
  .detail .detail-title
    color: #333333
    font-weight: bold
    margin-bottom: 15px
  .detail>.detail-list
    color: #333333
    text-indent: 18px
    line-height: 20px
    margin-bottom: 15px
  .detail>.detail-list:last-child
    margin-bottom: 0




</style>



