<template>
    <div id="AddRecord">
      <div class="addrecord_panel" ref="tabbarWrapper" :style="{height:this.ScreenHeight+'px'}">
        <ul class="ul_panel" :style="{height:(this.tabbarConentHeight + 'px')}">
          <li v-for="(item,index) in this.list">
            <p><span class="addrecord_title">姓名：</span><span class="addrecord_name">{{item.name}}</span></p>
            <p>
              <span class="addrecord_title">状态：</span>
              <span class="addrecord_status" style="color: #5baaec" v-show="item.status === 0">已挂号</span>
              <span class="addrecord_status" style="color: #ffaa12" v-show="item.status === 1">待分配</span>
              <span class="addrecord_status" style="color: #333333" v-show="item.status === 2">已就诊</span>
              <span class="addrecord_status" style="color: #ff4668" v-show="item.status === 3">取消</span>
            </p>
            <p><span class="addrecord_title">诊疗卡号：</span><span class="addrecord_cardnumber">{{item.cardNumber}}</span></p>
            <p><span class="addrecord_title">推送时间：</span><span class="addrecord_pushtime">{{item.pushTime}}</span></p>
          </li>
        </ul>
      </div>
    </div>
</template>

<script>
  import BScroll from 'better-scroll';
    export default{
      name: 'AddRecord',
      components: {
      },
      data() {
        return {
          ScreenHeight: document.body.clientHeight,
          tabbarConentHeight: 0,
            list: [
              {
                name: '李凯',
                status: 0,
                cardNumber: '32523566',
                pushTime: '2017-10-30 09:00'
              },
              {
                name: '李凯',
                status: 1,
                cardNumber: '32523566',
                pushTime: '2017-10-30 09:00'
              },
              {
                name: '李凯',
                status: 2,
                cardNumber: '32523566',
                pushTime: '2017-10-30 09:00'
              },
              {
                name: '李凯',
                status: 3,
                cardNumber: '32523566',
                pushTime: '2017-10-30 09:00'
              },
              {
                name: '李凯',
                status: 2,
                cardNumber: '32523566',
                pushTime: '2017-10-30 09:00'
              },
              {
                name: '李凯',
                status: 3,
                cardNumber: '32523566',
                pushTime: '2017-10-30 09:00'
              }
      ]
        };
      },
      methods: {
        initBScroll: function () {
          if (!this.$refs.tabbarWrapper) {
            return;
          }
          /* 利用better-scroll处理滑动 */
          this.mScroll = new BScroll(this.$refs.tabbarWrapper, { bounce: false, scrollX: false, scrollY: true, click: true });
        },
        initTabtWidth: function () {
          // 获取屏幕高度
//          this.ScreenHeight = window.screen.availHeight;
          let conentHeight = 15;
          let childrenList = document.getElementsByClassName('ul_panel')[0].children;
          for (let i = 0; i < childrenList.length; i++) {
            conentHeight += 15 + childrenList[i].offsetHeight; // 加上子项偏移量
          }
          // 获取内容高度
          this.tabbarConentHeight = conentHeight;
        }
      },
      mounted () {
        const that = this;
        window.onresize = () => {
          return (() => {
            window.screenHeight = document.body.clientHeight;
          that.ScreenHeight = window.screenHeight;
          console.log(that.ScreenHeight);
        })();
        };
        this.initTabtWidth();
        this.$nextTick(() => {
          this.initBScroll();
        });
      },
      watch: {
        ScreenHeight (val) {
          if (!this.timer) {
            this.ScreenHeight = val;
            this.timer = true;
            let that = this;
            setTimeout(function () {
              that.timer = false;
            }, 400);
          }
        }
      }
    };
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../common/stylus/index.styl"
  #AddRecord
    background-color: #f5f5f5
    overflow-y: auto
  .addrecord_panel
    padding: 15px
  #AddRecord ul li
    height: 142px
    background-color: #ffffff
    margin-bottom: 15px
    padding: 15px 0 15px 15px
    box-sizing: border-box
    border-radius: 5px
    font-size: 15px
  #AddRecord ul li p
    margin-bottom: 20px
  .addrecord_title
    color: #666666
  .addrecord_name,.addrecord_cardnumber,.addrecord_pushtime
    color: #333333


</style>



