<template>
  <div id="div_plan_info">

    <div  v-if="showLoadding"  class="loading">
      <mt-spinner type="triple-bounce" color="#3293BE" style="text-align: center" ></mt-spinner>
    </div>
    <div v-else-if="this.planDataList.length > 0">
      <div class="top_tilte ">
          <!-- 固定行 -->
           <div class="plan_row_title fixed_row">
               <span class="row_item" style="color:white">.</span>
           </div>
           <div class="plan_row_title row_list" ref="rowTitleWrapper">
             <div class="row_title_conent" :style="{width:(this.conentWidth + 'px')}"  >
               <span class="row_item" v-for="(item,index) in this.totalDate"> {{item}}</span>
             </div>
           </div>
        </div>
       <div class="plan_data_list" :style="{height:(this.planDataListHeight + 'px')}">
         <!-- 固定列 -->
         <div class="fixed_column" ref="fixedColumnWrapper">
           <ul class="fixed_column_conent">
                <li class="fixed_row_item" v-for="(item, index) in this.totalTitle">
                  <span :style="{fontSize: (item.length >10 ? '12px': '15px')}">{{item}}</span>
                </li>
           </ul>
         </div>

         <div class="data_column" ref="dataColumsWrapper">
           <div class="list_row_conent"  :style="{width:(this.conentWidth + 'px')}">
             <p class="data_list_row" v-for="row in this.planDataList">
               <span class="list_row_column" v-for="(item,index) in row.showPlanDateList" :key="index" :style="{color:(item === '0')? '#fff': '#333'}">
                  <img src="~assets/gou@2x.png" v-if="item === '1'" width="13px" height="9px"/> <label v-else> {{ item }}</label>
               </span>
             </p>
           </div>
         </div>
       </div>
    </div>
    <div class="empty_data"  v-else>
      暂无数据
    </div>
  </div>
</template>
<script>
  import BScroll from 'better-scroll';
  import { Spinner, MessageBox } from 'mint-ui';
  import { netWorkPost } from '../common/js/netjob';
  export default {
      name: 'ProjectPlanInfo',
      components: {
      'mt-spinner': Spinner
      },
      methods: {
         _initBScroll: function () {
           if (!this.$refs.rowTitleWrapper) {
               return;
           }
           /* 利用better-scroll处理滑动 */
           // 首行固定  横向滑动
           this.rowTitleWrapperScroll = new BScroll(this.$refs.rowTitleWrapper, { bounce: false, momentum: false });
           // 首列固定  纵向滑动
           this.fixedColumnWrapperScroll = new BScroll(this.$refs.fixedColumnWrapper, { bounce: false, scrollY: false, momentum: false });
           // 滑动内容区域
          this.dataColumsWrapperScroll = new BScroll(this.$refs.dataColumsWrapper, {
              scrollX: true,
              scrollY: true,
              bounce: false, //  当滚动超过边缘的时候会有一小段回弹动画。设置为 true 则开启动画
              momentum: false,
              probeType: 2
          });
          let lastX = 0;
          let lastY = 0;
          this.dataColumsWrapperScroll.on('scroll', (pos) => {
              let scrollX = Math.abs(Math.abs(Math.round(pos.x)) - lastX);
              let scrollY = Math.abs(Math.abs(Math.round(pos.y)) - lastY);
              if (scrollX < 2 && scrollY < 2) return;    // 避免细微滑动
              if (scrollX > scrollY) {
                this.rowTitleWrapperScroll.scrollTo(Math.round(pos.x), 0);
              } else if (scrollX < scrollY) {
                this.fixedColumnWrapperScroll.scrollTo(0, Math.round(pos.y));
              } else {
                this.rowTitleWrapperScroll.scrollTo(0, 0);
               // console.log('00');
              }
           //  console.log('scrollX ---->' + scrollX + 'scrollY---->' + scrollY);
             lastX = Math.abs(Math.round(pos.x));  // 记录上一次滑动的X坐标
             lastY = Math.abs(Math.round(pos.y));  // 记录上一次滑动的Y坐标
          //   console.log(pos);
          });
         },
         _initPlanDataListHeight: function () {
           // 获取屏幕高度
           let bodyHeight = window.screen.availHeight;
           // 顶部tab高度
           let tabHeight = document.getElementById('tab_panel').offsetHeight;
           // 获取title 高度
           let titleHeight = 40;
           this.planDataListHeight = bodyHeight - tabHeight - titleHeight - 10;
         },
         loadingProjectPlan: function () {
           let projectID = this.$route.params.projectID;
           let doctorId = this.$route.params.doctorId;
           let pam = {
             url: 'project/projectPlan',
             data: {
               projectId: projectID,
               doctorId: doctorId
             },
             before: () => {
               this.showLoadding = true;
             },
             success: (res) => {
               let response = res.data;
               if (response.status > 0) {
                 this.fail(res);
                 return;
               }
               this.totalTitle = response.data.totalTitle;
               this.totalDate = response.data.totalDate;
               this.planDataList = response.data.info;
               this.getPlan(this.planDataList, this.totalDate);
               this.conentWidth = this.totalDate.length * 100;
             },
             fail: (res) => {
               MessageBox.alert('加载失败!', '提示');
             },
             after: () => {
               this.showLoadding = false;
             }
           };
           pam.before();
           netWorkPost(pam);
         },
         getPlan: function(planDataList, totalDate) {
           if (planDataList.length === 0) return;
           planDataList.map((e) => {
             let planDateList = [];
             for (let i = 0; i < totalDate.length; i++) {
               let isExist = false;
               for (let j = 0; j < e.date.length; j++) {
                   if (totalDate[i] === e.date[j]) {
                       isExist = true;
                       break;
                   }
               }
               if (isExist) {
                 planDateList.push('1');
               } else {
                 planDateList.push('0');
               }
             }
             e.showPlanDateList = planDateList;
           });
        }
      },
      created () {
        this.loadingProjectPlan();
      },
      mounted () {
          this._initPlanDataListHeight();
      },
      updated () {  // 组件 DOM 已经更新
        this._initBScroll();
      },
      computed: {

      },
      data () {
          return {
            conentWidth: 0,
            showLoadding: false,
            planDataListHeight: 0, // 内容高度
            totalTitle: [],  // 计划项目标题
            totalDate: [],  // 计划日期
            planDataList: [] // 计划列表
          };
      }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  #div_plan_info
    padding-top:10px
    .top_tilte
      display:flex
      width:100%
      overflow:hidden
      border-bottom:1px solid  #e5e5e5
      background-color:white
      .fixed_row
        width:100px
      .row_list
        flex:1
        white-space: nowrap;//强制不折行
        overflow: hidden;//控制溢出隐藏
        font-size:0px
      .plan_row_title
         float:left
        .row_item
          box-sizing:border-box
          width:100px
          min-width:100px
          display:inline-block
          line-height:40px
          text-align:center
          font-size:15px
          color:#33333
          border-right:1px solid #e5e5e5
          background-color:#ffffff
    .plan_data_list
      display:flex
      width:100%
      height:100%
      overflow:hidden
      background-color:white
      .fixed_column
        width:100px
        font-size:0px
        height:100%
        .fixed_row_item
          display:flex
          align-items:center
          justify-content:center
          box-sizing:border-box
          width:100px
          height:50px
          text-align:center
          font-size:15px
          color:#33333
          background-color:#ffffff
          border-bottom:1px solid #e5e5e5
          border-right:1px solid #e5e5e5
          overflow:hidden
      .data_column
        width:314px
        overflow:hidden;
        height:100%
        .data_list_row
          white-space: nowrap;//强制不折行
          font-size: 0px
          .list_row_column
            display:inline-block
            box-sizing:border-box
            width:100px
            height:50px
            line-height:50px
            text-align:center
            font-size:15px
            color:#33333
            background-color:#ffffff
            border-right:1px solid #e5e5e5
            border-bottom:1px solid #e5e5e5
    .empty_data
       text-align:center;
       width:100%
       padding-top:20px
</style>



