<template>
    <div id="SearchInfo">
      <ul>
        <li>
          <div><span class="Info-title">检查报告：</span><input class="Info-value" type="text" placeholder="请输入检查编号" ></div>
        </li>
        <li>
          <div><span class="Info-title">项目组合：</span><input class="Info-value" type="text" placeholder="请输入组合名称" ></div>
        </li>
        <li>
          <div>
            <span class="Info-title">起始时间：</span>
            <div class="Info-value">{{date | datefmt}}</div>
            <!--<input class="Info-value" type="text" placeholder="2017-12-24" >-->
            <img src="../assets/calendar@2x.png" style="width: 22px;height: 22px" @click="ChoseDate()" >
          </div>
        </li>
        <li>
          <div>
            <span class="Info-title">截止时间：</span>
            <div class="Info-value">{{date | datefmt}}</div>
            <!--<input class="Info-value" type="text" placeholder="2017-12-24" >-->
            <img src="../assets/calendar@2x.png" style="width: 22px;height: 22px" @click="ChoseDate()">
          </div>
        </li>
      </ul>
      <div class="button_content">
        <mt-button type="primary" size="large" @click="CheckOut()">查询</mt-button>
      </div>

      <mt-popup class="popup" v-model="popupVisible" position="bottom">
        <calendar class="calendar-wrapper" ></calendar>
      </mt-popup>

    </div>
</template>

<script>
  import Calendar from '@/components/calendar/Calendar';
  import { MessageBox } from 'mint-ui';
    export default{
      name: 'SearchInfo',
      components: {
        [Calendar.name]: Calendar
      },
      data() {
        return {
          popupVisible: false,
          date: new Date()
        };
      },
      methods: {
        ChoseDate: function () {
          this.popupVisible = !this.popupVisible;
        },
        CheckOut: function () {
          MessageBox('提示', '程序猿正在拼命开发中...');
        }
//        onMonthChanged(date) {
//          console.log(date.getTime());
//          if (window.jsi != null) {
//            window.jsi.onMonthChanged(date.getTime());
//          }
//          window.document.title = date.getFullYear() + '年' + (date.getMonth() + 1) + '月';
//        }
      },
      filters: {
          datefmt: function (input) {
            let res = '';
            let year = input.getFullYear();
            let month = input.getMonth() + 1;
            let day = input.getDate();
            month = month < 10 ? '0' + month : month;
            day = day < 10 ? '0' + day : day;
            res = year + '-' + month + '-' + day;
            return res;
          }
      }
    };
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../common/stylus/index.styl"
  #SearchInfo
    background-color: #f5f5f5
  #SearchInfo ul
    padding-top: 15px
  #SearchInfo ul>li
    background-color: #ffffff
    height: 50px
    border-bottom-color: #f5f5f5
    border-bottom-width 1px
    border-bottom-style solid
    padding: 0 15px
    display: flex
    align-items: center

  #SearchInfo ul>li div
    display: flex
    flex-direction: row
    align-items: center
    width: 100%
  .Info-title
    color: #333333
    font-size: 15px
    white-space: nowrap
  input:focus
    outline:none
  input::-webkit-input-placeholder
    font-size: 15px
    color: #b2b2b2
  input:-moz-placeholder
    font-size: 15px
    color: #b2b2b2
  input::-moz-placeholder
    font-size: 15px
    color: #b2b2b2
  input:-ms-input-placeholder
    font-size: 15px
    color: #b2b2b2
  .Info-value
    flex: 1
    color: #b2b2b2
  .button_content
    height: 45px
    margin-top: 70px
    display: flex
    flex-direction: row
    justify-content: center
    align-items: center
    padding: 0 15px
    box-sizing: border-box
  .mint-button--large
    flex: 1
    height: 100%
    border-radius: 5px
  .popup
    width: 100%




</style>



