<template>
  <div id="tab_panel">
    <p class="top_tab">
        <span class="tab_item" v-for="(item,index) in this.tabItems" :class="{isCheck: index === cureentIndex}" @click="tabItemClick(index)" >
        {{item.title}}
        </span>
    </p>
    <p class="top_tab_line">
      <span class="tab_item_line" v-for="(item,index) in this.tabItems"
            :style="{borderBottomColor:(index === cureentIndex ? '#5baaec': '#e5e5e5'),
                     borderBottomWidth:(index === cureentIndex ? '2px':'1px')}"></span>
    </p>
  </div>
</template>
<script>
  import _ from 'lodash';
  export default {
    name: 'MyTabbar',
    props: ['tabItems'],
    methods: {
      tabItemClick: _.debounce(function (index) {
        if (index === this.cureentIndex) return;
        this.cureentIndex = index;
        // 触发父类绑定的 对应事件
        this.$emit('introduce', this.tabItems[index]);
        // this.status = this.tabItems[index].status; // 改变状态值
      }, 200)
    },
    data () {
        return {
          cureentIndex: 0
        };
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  #tab_panel
    background-color:white
    .isCheck
      color:#5baaec
    .top_tab
      display:flex
      color:#999
      .tab_item
        flex:1
        text-align:center
        height:45px
        line-height:45px
        position:relative
        font-size:15px
        font-weight:600
    .top_tab_line
      display:flex
      .tab_item_line
        flex:1
        border-bottom-style:solid

</style>



