<template>
  <div id="imageResultDetail">
    <div id="tab_panel" ref="tabbarWrapper"  :style="{width:this.tabbarWidth+'px'}">
      <p class="top_tab" :style="{width:(this.tabbarConentWidth + 'px')}">
        <span class="tab_item" v-for="(item,index) in this.tabItems" :class="{isCheck: index === current}" @click="tabItemClick(index)"
        :style="{color:(index===current? '#5baaec': '#333333'),backgroundColor:(index===current? '#ffffff':'#f5f5f5')}">{{item.title}}</span>
      </p>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll';
export default {
    name: 'ImageResultDetail',
    data () {
        return {
          tabbarWidth: 0,
          tabbarConentWidth: 980,
          current: 0,
          tabItems: [
            { title: '图文报告', status: 1, isCheck: true },
            { title: '图像', status: 2, isCheck: false },
            { title: '检查明细及文本报告', status: 3, isCheck: false },
            { title: '检查结果', status: 4, isCheck: false },
            { title: '检查推断', status: 5, isCheck: false }
          ]
        };
    },
    methods: {
      tabItemClick: function (index) {
        if (this.current === index) return;
        this.current = index;
      },
      initBScroll: function () {
        if (!this.$refs.tabbarWrapper) {
          return;
        }
        /* 利用better-scroll处理滑动 */
        this.mScroll = new BScroll(this.$refs.tabbarWrapper, { bounce: false, scrollX: true, scrollY: false, click: true });
      },
      initTabtWidth: function () {
        // 获取屏幕宽度
        this.tabbarWidth = window.screen.availWidth;
        let conentWidth = 15;
        let childrenList = document.getElementsByClassName('top_tab')[0].children;
        for (let i = 0; i < childrenList.length; i++) {
          conentWidth += 15 + childrenList[i].offsetWidth; // 加上子项偏移量
        }
        console.log(conentWidth);
        // 获取内容宽度
        this.tabbarConentWidth = conentWidth;
      }
    },
    mounted () {
      this.initTabtWidth();
      this.$nextTick(() => {
        this.initBScroll();
      });
    }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../common/stylus/mixin.styl";
  #imageResultDetail
    background-color: #f5f5f5
  #tab_panel
    background-color:white
    position:fixed
    top:0px
    left:0px
    z-index:9998
    white-space: nowrap;//强制不折行
    overflow:hidden
    .isCheck
      border: 1px solid #5baaec
    .top_tab
      display: flex
      flex-direction: row
      align-items: center
      height: 50px
      /*padding-left:15px*/
      .tab_item
        display:inline-block
        height: 25px
        width: 115px
        background-color: #f5f5f5
        line-height:25px
        border-radius: 5px
        text-align:center
        font-size:12px
        font-weight:600
        box-sizing: border-box
        margin-left: 15px



</style>



