function createDates(dates, length) {
  if (dates === undefined || dates === null) {
    dates = [];
  }
  dates.splice(0, dates.length);
  var step = 24 * 60 * 60 * 1000;
  var timestamp = Date.now();
  var date = null;
  var item = null;
  var month = 0;
  var _date = 0;
  for (var i = 0; i < length; i++) {
    date = new Date(timestamp - step * i);
    item = {};
    switch (date.getDay()) {
      case 0:
        item.weekday = '周日';
        break;
      case 1:
        item.weekday = '周一';
        break;
      case 2:
        item.weekday = '周二';
        break;
      case 3:
        item.weekday = '周三';
        break;
      case 4:
        item.weekday = '周四';
        break;
      case 5:
        item.weekday = '周五';
        break;
      case 6:
        item.weekday = '周六';
        break;
      default:
        item.weekday = '';
        break;
    }
    month = date.getMonth() + 1;
    month = month < 10 ? '0' + month : month;
    _date = date.getDate();
    _date = _date < 10 ? '0' + _date : _date;
    item.dateStr = month + '-' + _date;
    item.dayStr = _date;
    item.date = date;
    dates.push(item);
  }
  return dates;
}
