<template>
<div id="div_project_detail">
  <t-tabbar :tabItems="this.tabItems" @introduce="tabbarItemClick" class="tabbar"></t-tabbar>
  <div style="padding-top: 47px">
    <component :is="componentData" ></component>
  </div>
</div>
</template>
<script>
  import MyTabbar from './Tabbar/MyTabbar.vue';
  import Detail from './FollowupDetail.vue';
  import PlanInfo from './ProjectPlanInfo.vue';
  export default {
    name: 'ProjectDetail',
    components: {
      't-tabbar': MyTabbar,
      'Detail': Detail,
      'PlanInfo': PlanInfo
    },
    methods: {
      tabbarItemClick: function (item) {
        this.componentData = item.componentsName;
      }
    },
    data () {
      return {
        tabItems: [
          { title: '信息', status: 1, componentsName: 'Detail' },
          { title: '计划', status: 2, componentsName: 'PlanInfo' }
        ],
        componentData: 'Detail'
      };
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  #div_project_detail
    .tabbar
      position:fixed
      top:0px
      left:0px
      width:100%
      z-index:9999
</style>



