<template>
    <div class="HospitalRecord">
      <!--分类-->
      <div class="cate">
        <ul v-bind="{style:'width:'+ulWidth+'px'}">
          <li v-for="(item,index) in this.cates" :class="{selected: index===currentIndex}"  @click="ItemClick(index)">{{item}}</li>
        </ul>
      </div>
      <!--患者详情-->
      <div class="patientInfo">
        <div class="patient-date">
          入院日期：<span>{{data.ruyuan}}</span>
        </div>
        <div class="patient-item">
          <p>
            <span>病史陈述者：</span>{{data.name}}
          </p>
          <p>
            <span>主诉：</span>{{data.zhusu}}
          </p>
          <p>
            <span>现病史：</span>
            {{data.xianbingshi}}
          </p>
          <p>
            <span>既往史：</span>{{data.zhusu}}
          </p>
          <p>
            <span>个人史：</span>{{data.zhusu}}
          </p>
          <p>
            <span>婚育史：</span>{{data.zhusu}}
          </p>
          <p style="margin-bottom: 0">
            <span>家族史：</span>{{data.zhusu}}
          </p>
        </div>
        <div style="margin: 15px 0">
          <div class="PhysicalCheck">体格检查</div>
          <div class="PhysicalCheckDetail">
            <p style="margin-bottom: 8px;">
              <span>体温：</span><span>37.5C，</span><span>脉搏：</span><span>80次/分，</span>
              <span>呼吸：</span><span>18次/分</span>
            </p>
            <p>
              <span>血压：</span>80/120mmHg,
              <span>体重：</span>60kg。
            </p>
            <p style="margin-top: 17px">
              发育正常，营养良好，体型为匀称型，神志清楚，精神状态良好，自动体味，面色红润，全身皮肤无黄染，无皮疹，无皮下出血。
            </p>
          </div>
        </div>
        <div style="margin: 15px 0">
          <div class="PhysicalCheck">专科检查</div>
          <div class="CheckInfo">
            <img src="http://p3.so.qhimgs1.com/bdr/_240_/t018f700e3623ab9833.jpg" style="width: 100%">
          </div>
        </div>
        <div style="margin: 15px 0">
          <div class="PhysicalCheck">听力检查</div>
          <table border="1">
            <tr style="height: 70px">
              <td>频率</td>
              <div class="EyesCheck">
                <td colspan="2">频率</td>
                <td>频率</td>
                <td>频率</td>
              </div>

            </tr>
            <tr style="height: 35px">
              <td>频率</td>
              <td>频率</td>
              <td>频率</td>
              <td>频率</td>
              <td>频率</td>
            </tr>
            <tr style="height: 35px">
              <td>频率</td>
              <td>频率</td>
              <td>频率</td>
              <td>频率</td>
              <td>频率</td>
            </tr>
            <tr style="height: 35px">
              <td>频率</td>
              <td>频率</td>
              <td>频率</td>
              <td>频率</td>
              <td>频率</td>
            </tr>
          </table>

        </div>

        <div class="OtherCheck">
          <p>其他检查：暂无</p>
          <p>辅助检查：暂无</p>
          <div class="Submit_button">
            <mt-button type="default" size="large">确定诊断</mt-button>
            <mt-button style="margin-left: 10px" type="default" size="large">初步诊断</mt-button>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
  export default {
      name: 'HospitalRecord',
      components: {
      },
       data() {
            return {
              currentIndex: 0,
              cates: ['16/4/9', '16/4/8', '16/4/7', '17/4/6'], // 用来存储看病日期数据的数组
              ulWidth: 0,
              data: {
                name: '李凯',
                sex: '1',
                nation: '汉族',
                age: '20',
                marriage: '1',
                department: '某某科室',
                bed: '5',
                inpatientNumber: '234235',
                zhiye: '企业普通员工',
                chushengdi: '广东省广州市',
                ruyuan: '2017-10-30 11:30',
                chuyuan: '2017-11-23 12:00',
                chenshuzhe: '密密麻麻',
                zhusu: '对声反应差伴学语异常4年',
                xianbingshi: '伤口上客户电话卡卡和历史来看哈都上客户电话卡卡和历上客户电话卡卡和历史来看哈都是老客户雕龙画凤呼呼啦'

              }
            };
        },
      created() {
           // 获取日期数据分类
        this.getcates();
      },
      methods: {
          getcates() {
              // 实现当前日期分类ul的总宽度=日期个数*每个日期宽度
            let w = 60;
            let count = this.cates.length;
            this.ulWidth = w * count;
          },
          ItemClick(index) {
            this.currentIndex = index;
          }
      }
    };
</script>
<style lang="stylus" rel="stylesheet/stylus" >
  @import "../common/stylus/index.styl"
  .HospitalRecord
    background-color: #f5f5f5
    .cate
      height: 50px
      background-color: #ffffff
      overflow-x: auto
      margin: 15px 0
      .selected
        color: #333
    .cate ul
      padding-left: 15px
      display: flex
      flex-direction: row
    .cate ul li
      list-style: none
      display: inline-block
      text-align: center
      line-height: 50px
      margin-right: 35px
      color: #999999
    .patientInfo
      height: auto
      .patient-date
        line-height: 50px
        background-color: #ffffff
        font-size: 15px
        padding-left: 15px
        color: #999999
       span
        color: #666666
    .patient-item
      height: auto
      background-color: #ffffff
      margin-top: 1px
      padding: 17px 15px
    .patient-item p
      color: #666666
      margin-bottom: 20px
      line-height: 30px
    .patient-item p span
      color: #999999
    .PhysicalCheck
      height: 50px
      font-size: 15px
      padding-left: 15px
      font-weight: bold
      box-sizing: border-box
      color: #333333
      line-height: 50px
      background-color: #ffffff
      margin-bottom: 1px
    .PhysicalCheckDetail
      padding: 17px 15px
      background-color: #ffffff
    .PhysicalCheckDetail p
      font-size: 15px
      color: #333333
      &>span
        color: #666666
    table
      width: 100%
      background-color: #ffffff
      &>tr
        height: 35px
        text-align: center
        display: flex
        &>td:first-child
          width: 100px
    .EyesCheck
      display: flex
      flex: 1
      flex-direction: row
      &>td
        flex: 1


    .OtherCheck
      padding: 20px 15px 0 15px
      background-color: #ffffff
      &>p
        margin-bottom: 20px
        /*margin-left: 15px*/
      &>.Submit_button
        display: flex
        flex-direction: row
        margin-top: 26px
        margin-bottom: 40px
        &>.mint-button
          flex: 1
          height: 35px
          width: 167px
          &.mint-button--default
            color: #333333
            background-color: #f5f5f5




</style>



