<template>
  <div id="div_project_team" v-windowTitle  data-title="项目成员">
    <div  v-if="showLoadding"  class="loading">
      <mt-spinner type="triple-bounce" color="#3293BE" style="text-align: center" ></mt-spinner>
    </div>
    <div class="p_item_panel border-1px" v-for="(item,index) in this.pListData" v-else-if="this.pListData.length > 0">
      <p class="p_item border-1px">
        <span class="p_item_tilte">姓名：</span> <span class="p_item_conent"> {{item.name}}</span>
      </p>
      <p class="p_item border-1px">
        <span class="p_item_tilte">角色：</span> <span class="p_item_conent">{{item.role}}</span>
      </p>
      <p class="p_item border-1px">
        <span class="p_item_tilte">电话：</span> <span class="p_item_conent"> {{item.phoneNumber}}</span>
      </p>
      <p class="p_item border-1px">
        <span class="p_item_tilte">邮箱：</span> <span class="p_item_conent"> {{item.email}}</span>
      </p>
    </div>
    <div class="empty_data"  v-else>
      暂无数据
    </div>
  </div>
</template>
<script>
  import { Spinner, MessageBox } from 'mint-ui';
  import { netWorkPost } from '../common/js/netjob';
  export default {
    name: 'ProjectTeamInfo',
    components: {
      'mt-spinner': Spinner
    },
    methods: {
      loadTeamInfo: function (val) {
        let vm = this;
        let pam = {
          url: 'project/projectMember',
          data: {},
          before: function () {
            vm.showLoadding = true;
          },
          success: function (res) {
            let response = res.data;
            if (response.status > 0) {
              this.fail(res);
              return;
            }
            vm.pListData = response.data.member;
          },
          fail: function (res) {
            MessageBox.alert('加载失败!', '提示');
          },
          after: function () {
            vm.showLoadding = false;
          }
        };
        Object.assign(pam, val);
        pam.before();
        netWorkPost(pam);
      }
    },
    mounted: function () {
      let projectId = this.$route.query.projectId;
      let doctorId = this.$route.query.doctorId;
      this.loadTeamInfo({
        data: {
          doctorId: doctorId,
          projectId: projectId
        }
      });
    },
    data () {
      return {
        showLoadding: false,
        pListData: []
      };
    }
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
  @import "../common/stylus/mixin.styl";
  #div_project_team
    background-color:#e5e5e5
    padding-top:15px
    .loading
      margin-top:10px
    .p_item_panel
      background-color:white
      margin-bottom:15px
      border-top-1px(#e5e5e5)
      .p_item
        height:45px
        line-height:45px
        padding:0px 15px
        font-size:15px
        color:#666
        border-bottom-1px(#e5e5e5)
        .more_icon
          float:right
          margin-top:10px
        .p_item_conent
          color:#333333
    .empty_data
      text-align:center;
      width:100%
      padding-top:20px
</style>



