import Vue from 'vue';
import Router from 'vue-router';
import ApplyInfo from '@/components/ApplyInfo';
import ReviewInfo from '@/components/ReviewInfo';
import FollowupDetail from '@/components/FollowupDetail';
import ProjectTeamInfo from '@/components/ProjectTeamInfo';
import DataBaseField from '@/components/DataBaseField';
import FilterInfo from '@/components/FilterInfo';
import ProjectDetail from '@/components/ProjectDetail';
import ProjectPlanInfo from '@/components/ProjectPlanInfo';
import Register from '@/components/register/Register';
import HospitalRecord from '@/components/HospitalRecord';
import CourseInfo from '@/components/CourseInfo';
import VisitsInfo from '@/components/VisitsInfo';
import ImageResult from '@/components/ImageResult';
import ImageResultDetail from '@/components/ImageResultDetail';
import OtherCheckResult from '@/components/OtherCheckResult';
import CheckResult from '@/components/CheckResult';
import OperationInfo from '@/components/OperationInfo';
import PatientDetail from '@/components/PatientDetail';
import MedicalRecordHome from '@/components/MedicalRecordHome';
import DoctorAdvice from '@/components/DoctorAdvice';
import ThreeTestSheet from '@/components/ThreeTestSheet';
import Medication from '@/components/Medication';
import ExamResults from '@/components/ExamResults';
import BusinessInfo from '@/components/BusinessInfo';

import HostpitalPatientDetail from '@/components/HostpitalPatientDetail';
import FollwupCase from '@/components/FollwupCase';
import SearchInfo from '@/components/SearchInfo';
import DataStorage from '@/components/DataStorage';
import AddRecord from '@/components/AddRecord';
import MessageInfo from '@/components/MessageInfo';
Vue.use(Router);
export default new Router({
  routes: [
    {
      path: '/',
      name: 'ApplyInfo',
      component: ApplyInfo
    },
    {
      path: '/reviewinfo',
      name: 'ReviewInfo',
      component: ReviewInfo
    },
    {
      path: '/followupdetail/:doctorId/:projectID/:projectName',
      name: 'FollowupDetail',
      component: FollowupDetail
    },
    {
      path: '/projectteaminfo',
      name: 'ProjectTeamInfo',
      component: ProjectTeamInfo
    },
    {
      path: '/databasefield',
      name: 'DataBaseField',
      component: DataBaseField
    },
    {
      path: '/filterinfo',
      name: 'FilterInfo',
      component: FilterInfo
    },
    {
      path: '/projectdetail/:doctorId/:projectID/:projectName',
      name: 'ProjectDetail',
      component: ProjectDetail
    },
    {
      path: '/projectplaninfo',
      name: 'ProjectPlanInfo',
      component: ProjectPlanInfo
    },
    {
      path: '/register/:doctorId/:patientId',
      name: Register.name,
      component: Register
    },
    {
      path: '/visitsinfo',
      name: VisitsInfo,
      component: VisitsInfo
    },
    {
      path: '/hospitalrecord',
      name: HospitalRecord,
      component: HospitalRecord
    },
    {
      path: '/courseinfo',
      name: CourseInfo,
      component: CourseInfo
    },
    {
      path: '/imageresult',
      name: ImageResult,
      component: ImageResult
    },
    {
      path: '/imageresultdetail',
      name: ImageResultDetail,
      component: ImageResultDetail
    },
    {
      path: '/othercheckresult',
      name: OtherCheckResult,
      component: OtherCheckResult
    },
    {
      path: '/operationinfo',
      name: OperationInfo,
      component: OperationInfo
    },
    {
      path: '/checkresult',
      name: CheckResult,
      component: CheckResult
    },
    {
      path: '/patientdetail',
      name: PatientDetail,
      component: PatientDetail
    },
    {
      path: '/medicalrecordhome',
      name: MedicalRecordHome,
      component: MedicalRecordHome
    },
    {
      path: '/doctoradvice',
      name: DoctorAdvice,
      component: DoctorAdvice
    },
    {
      path: '/threetestsheet',
      name: ThreeTestSheet,
      component: ThreeTestSheet
    },
    {
      path: '/medication',
      name: Medication,
      component: Medication
    },
    {
      path: '/examresults',
      name: ExamResults,
      component: ExamResults
    },
    {
      path: '/businessinfo',
      name: BusinessInfo,
      component: BusinessInfo
    },
    {
      path: '/hostpitalpatientdetail',
      name: HostpitalPatientDetail,
      component: HostpitalPatientDetail
    },
    {
      path: '/follwupcase',
      name: FollwupCase,
      component: FollwupCase
    },
    {
      path: '/searchinfo',
      name: SearchInfo,
      component: SearchInfo
    },
    {
      path: '/datastorage',
      name: DataStorage,
      component: DataStorage,
      meta: {
        title: '资料存储'
      }
    },
    {
      path: '/addrecord',
      name: AddRecord,
      component: AddRecord,
      meta: {
        title: '加号记录'
      }
    },
    {
      path: '/messageinfo',
      name: MessageInfo,
      component: MessageInfo
    }

  ]
});



