<template>
    <div id="MessageInfo">
      <div v-for="(item,index) in this.list">
        <div class="messageinfo_panel" v-show="item.type === 0">
          <div class="message_top">
            <p class="message_title">{{item.title}}</p>
            <p class="message_patient">
              <span class="name">患者：</span><span class="value">{{item.patient}}</span>
            </p>
            <p class="message_crisis">
              <span class="name">危机值：</span><span class="value">{{item.crisis}}</span>
            </p>
          </div>
          <div class="message_center">
            <div>
              <p class="message_title">相关联系医生</p>
              <p class="name"  >刘学仁教授</p>
            </div>
            <img src="../assets/Telephone@2x.png" style="width: 27px;height: 27px">
          </div>
          <div class="message_bottom">
            <div>
              <p class="message_title">更多相关人员</p>
              <p class="name" v-for="(v,index) in item.relatedPerson">{{v.name}}{{v.duty}}</p>
            </div>
            <img src="../assets/Telephone@2x.png" style="width: 27px;height: 27px" @click="CallNumber()">
          </div>
        </div>
        <div class="messageinfo_panel" v-show="item.type === 1">
          <div class="message_top last_top">
            <p class="message_title">{{item.title}}</p>
            <p class="message_patient">
              <span class="name">项目：</span><span class="value">{{item.projectName}}</span>
            </p>
            <p class="message_crisis">
              <span class="name">审核状态：</span>
              <span class="value" style="color: #5baaec" v-show="item.status === 0">待审核</span>
              <span class="value" v-show="item.status === 1">已审批</span>
              <span class="value" v-show="item.status === 2">未通过</span>
            </p>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
    export default{
      name: 'MessageInfo',
      components: {
      },
      data() {
        return {
          list: [
            {
              type: 0,
              title: '危机值',
              patient: '成龙',
              crisis: '心脏跳动异常',
              relatedDoctor: {
                name: '刘学仁',
                duty: '教授',
                phoneNumber: '13289078900'
              },
              relatedPerson: [
                {
                    name: '卢一丁',
                    duty: '主治医师',
                    phoneNumber: '13289078900'
                },
                {
                    name: '丁一卢',
                    duty: '教授',
                    phoneNumber: '13289078900'
                }
              ]
            },
            {
              type: 1,
              title: '随访项目审核',
              projectName: '下颚溃烂离子分合证项目',
              status: 0
            }
          ]
      };
      },
      methods: {
        CallNumber: function () {
              alert(1);
          }

      }
    };
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../common/stylus/index.styl"
  #MessageInfo
    background-color: #f5f5f5
  .messageinfo_panel
    padding: 15px 15px 0 15px
  .message_top
    height: 93px
    background-color: #ffffff
    padding: 12px 15px
    box-sizing: border-box
    border-radius: 5px 5px 0 0
  .message_title
    font-size: 15px
    color: #333333
  .message_patient
    margin-top: 15px
    font-size: 14px
  .message_crisis
    margin-top: 10px
  .name
    color: #666666
    font-size: 14px
  .value
    color: #333333
  .message_center
    display: flex
    flex-direction: row
    align-items: center
    justify-content: space-between
    height: 71px
    background-color: #ffffff
    margin-top: 1px
    padding: 18px 15px
    box-sizing: border-box
  .message_center>div .name
    margin-top: 7px
  .message_bottom
    display: flex
    flex-direction: row
    height: auto
    align-items: center
    justify-content: space-between
    background-color: #ffffff
    margin-top: 1px
    padding: 18px 15px
    box-sizing: border-box
    border-radius: 0 0 5px 5px
  .message_bottom>div .name
    margin-top: 7px
  .last_top
    border-radius: 5px


</style>



